<template>
    <div>
        <mt-header title="整改情况分析" :fixed="true">
            <router-link to="/" slot="left">
                <mt-button>
                    <img class="header-img" src="static/image/ic_navbar_back@2x.png">
                </mt-button>
            </router-link>
            <router-link to="/" slot="right">
                <mt-button>
                    <img class="header-img" src="static/image/ic_navbar_home@2x.png">
                </mt-button>
            </router-link>
        </mt-header>
        <div class="has-header ">
            <div class="spliter"></div>
            <div class="part-title">
                <span></span>整改情况汇总（事件个数）
            </div>
            <div class="ratio-lr-time">
                <span>上期</span>
                <span>本期</span>
            </div>
            <div id="chart_ratio" class="ratiochart"></div>
            <div class="part-title">
                <span></span>整改率
            </div>
            <div class="rowbox">
                <div id="lastrectifypie" class="collectpie"></div>
                <div id="currentrectifypie" class="collectpie"></div>
            </div>
            <div class="part-title">
                <span></span>整改完成率
            </div>
            <div class="rowbox">
                <div id="lastrectifyfinishpie" class="collectpie"></div>
                <div id="currentrectiffinishypie" class="collectpie"></div>
            </div>
            <div style="height:1.4rem"></div>
        </div>
        <mt-tabbar :fixed="true">
            <span @click="showCompany" class="company-show">选择机构</span>
            <span @click="showYear" class="year-show">查询时间</span>
        </mt-tabbar>
        <!--机构弹窗-->
        <mt-popup
                v-model="popupVisible"
                class="popup"
                position="bottom">
            <div class="select-title">
                <span class="cancel" @click="cancel">取消</span>
                <span>选择机构</span>
                <span class="confirm" @click="confirmCompany">确认</span>
            </div>
            <div class="select-company">
                <button v-for="company in companys" @click="btnSelect(company)"
                        :class="[company.comCode==tempCompany.comCode?'selected':'']">{{company.codeName}}
                </button>
                <div class="blank-fill"></div>
            </div>
        </mt-popup>
        <!--年份选择弹窗-->
        <mt-popup
                v-model="popupYear"
                class="popup"
                position="bottom">
            <div class="select-title">
                <span class="cancel" @click="cancel">取消</span>
                <span>选择时间</span>
                <span class="confirm" @click="confirmYear">确认</span>
            </div>
            <mt-picker :slots="slots" @change="onValuesChange"></mt-picker>
        </mt-popup>
    </div>
</template>

<script>
    import axios from 'axios'

    export default {
        data() {
            return {
                selected: '1',
                regval: /\d+/,
                popupVisible: false,     //机构选择弹框
                popupYear: false,        //年份选择弹窗
                companys: [],             //所有机构数组
                startYear: 2015,
                endYear: 2035,
                yearList: [],
                tempCompany: {},         //所选机构
                currentCompany: {        //当前机构
                    comCode: "01",
                    codeName: "总公司全辖"
                },
                currentYear: '',
                tempYear: '',
                currentQuarter: '',
                tempQuarter: '',
                pieColor: [
                    {
                        start:'#F6DF9A',
                        end:'#E6B54D'
                    },{
                        start:'#6FF5E3',
                        end:'#32C9B0'
                    }
                ],
                pieColor1: [
                    {
                        start:'#50A2FF',
                        end:'#116DCF'
                    },{
                        start:'#B093FF',
                        end:'#7F61D9'
                    }
                ],
                initData: {},
                slots: [
                    {
                        flex: 1,
                        values: [],
                        className: 'slot1',
                        textAlign: 'right'
                    }, {
                        divider: true,
                        content: '-',
                        className: 'slot2'
                    }, {
                        flex: 1,
                        values: ["第一季度", "第二季度", "第三季度", "第四季度"],
                        className: 'slot4',
                        textAlign: 'left'
                    }
                ]

            }
        },
        created() {
            var date = new Date;
            this.currentYear = date.getFullYear();
            this.currentQuarter = Math.ceil((date.getMonth() + 1) / 3);
            this.drawRectification();
        },
        methods: {
            drawRectification() {
                axios.get('../static/json/Rectification.json').then(res => {
                    this.initData = res.data.initData;
                    this.drawRatio();
                    this.drawPie(this.initData.rectifyData.last,'lastrectifypie',"上期",this.pieColor1);
                    this.drawPie(this.initData.rectifyData.current,'currentrectifypie',"本期",this.pieColor);
                    this.drawPie(this.initData.RectifyFinishData.last,'lastrectifyfinishpie',"上期",this.pieColor1);
                    this.drawPie(this.initData.RectifyFinishData.current,'currentrectiffinishypie',"本期",this.pieColor);
                })
            },
            drawRatio(){
                var yAxisData = this.initData.data.map(value=>value.name);
                var seriesRightData = [];
                var seriesLeftData = this.initData.data.map(function(value){
                    var leftValue ;
                    if(value.left>=40){
                        leftValue = value.left
                    }else{
                        leftValue = {
                            value:value.left,
                            label:{
                                normal:{
                                    position:'left',
                                    color:'#3B4053',
                                }
                            }
                        };
                    }
                    if(value.right>=40){
                        seriesRightData.push(value.right)
                    }else{
                        seriesRightData.push({
                            value:value.right,
                            label:{
                                normal:{
                                    position:'right',
                                    color:'#3B4053'
                                }
                            }
                        });
                    }
                    return leftValue

                });
                var maxVal=0;
                for (var i = 0; i < this.initData.data.length; i++) {
                    //获取最大值
                    if (this.initData.data[i].left > maxVal) {
                        maxVal = this.initData.data[i].left;
                    }
                    if (this.initData.data[i].right > maxVal) {
                        maxVal = this.initData.data[i].right;
                    }
                }
                maxVal=Math.ceil(maxVal/10)*10;
                var option={
                    backgroundColor: '#fff',
                    tooltip: {
                        show:false,
                    },
                    grid:[{
                        show:false,
                        left:'3.73%',
                        top:'8.95%',
                        bottom:30*this.relativeRate,
                        width:"33.06%"
                    },{
                        show:false,
                        left:'50%',
                        top:'9.7%',
                        bottom:30*this.relativeRate,
                        width:'25.06%'
                    },{
                        show:false,
                        top:'8.95%',
                        bottom:30*this.relativeRate,
                        right:'3.73%',
                        width:"33.06%"
                    }],
                    yAxis: [{
                        show:true,
                        type: 'category',
                        position:'right',
                        axisLabel:{
                            show:false,
                            fontSize: 12,
                        },
                        axisLine:{
                            show:false
                        },
                        axisTick:{
                            show:false
                        },
                        splitLine:{
                            show:true,
                            lineStyle:{
                                color:'rgba(145,183,220,0.2)',
                            }
                        },
                        data: yAxisData,
                    },{
                        show:true,
                        gridIndex:1,
                        type: 'category',
                        position:'left',
                        axisLabel:{
                            show:true,
                            margin:0,
                            align:'center',
                            color:'rgba(59,64,83,1)',
                            fontSize: 22*this.relativeRate,
                            formatter:function(value,index){
                                if(value.length>=8){
                                    value = value.split("");
                                    value.splice(6,0,"\n");
                                    value = value.join("");
                                }else if(value.length==7){
                                    value = value.split("");
                                    value.splice(5,0,"\n");
                                    value = value.join("");
                                }else{

                                }
                                return value;
                            }
                        },

                        axisLine:{
                            show:false,
                        },
                        axisTick:{
                            show:false
                        },
                        data: yAxisData
                    },{
                        show:true,
                        gridIndex:2,
                        position:'left',
                        type: 'category',
                        axisLabel:{
                            show:false,
                            fontSize: 12,
                        },
                        axisLine:{
                            show:false
                        },
                        axisTick:{
                            show:false
                        },
                        splitLine:{
                            show:true,
                            lineStyle:{
                                color:'rgba(145,183,220,0.2)',
                            }
                        },
                        data: yAxisData,
                    }],
                    xAxis: [{
                        show:true,
                        type: 'value',
                        inverse:true,
                        max:maxVal,
                        axisLabel:{
                            show:false,
                        },
                        axisLine:{
                            show:false
                        },
                        axisTick:{
                            show:false
                        },
                        splitLine:{
                            show:true,
                            lineStyle:{
                                color:'rgba(145,183,220,0.2)',
                                type:'dashed'
                            }
                        },
                    },{
                        show:false,
                        gridIndex:1,
                    },{
                        show:true,
                        type: 'value',
                        gridIndex:2,
                        max:maxVal,
                        axisLabel:{
                            show:false,
                        },
                        axisLine:{
                            show:false
                        },
                        axisTick:{
                            show:false
                        },
                        splitLine:{
                            show:true,
                            lineStyle:{
                                color:'rgba(145,183,220,0.2)',
                                type:'dashed'
                            }
                        },
                    }],

                    series: [{
                        type:'bar',
                        name:'',
                        smooth:true,
                        barWidth:38*this.relativeRate,
                        itemStyle:{
                            normal:{
                                color:new this.echarts.graphic.LinearGradient(0, 0, 1, 0, [{
                                    offset: 0,
                                    color: '#69E4FA'
                                }, {
                                    offset: 1,
                                    color: '#38C5F3'
                                }], false),
                            }
                        },
                        label: {
                            normal: {
                                show: true,
                                color:'#fff',
                                fontSize:26*this.relativeRate,
                                position:'insideRight',
                                formatter: '{c}'
                            }
                        },
                        data:seriesLeftData
                    },{
                        type:'bar',
                        name:'',
                        barWidth:38*this.relativeRate,
                        smooth:true,
                        xAxisIndex: 2,
                        yAxisIndex: 2,
                        itemStyle:{
                            normal:{
                                color:new this.echarts.graphic.LinearGradient(0, 0, 1, 0, [{
                                    offset: 0,
                                    color: '#3FC391'
                                }, {
                                    offset: 1,
                                    color: '#86EACD'
                                }], false),
                            }
                        },
                        label: {
                            normal: {
                                show: true,
                                position:'insideLeft',
                                color:'#fff',
                                fontSize:26*this.relativeRate,
                                formatter: '{c}'
                            }
                        },
                        data:seriesRightData
                    }]
                };
                var chart = this.echarts.init(document.getElementById('chart_ratio'));
                chart.setOption(option);
            },
            drawPie(data,element,title,pieColor) {
                var assistData = [];
                var seriesData = this.getNewList(data, assistData,pieColor);
                var option = {
                    background: "#FFFFFF",
                    title: {
                        text: title,
                        x: 'center',
                        y: 'top'
                    },
                    tooltip: {
                        show: true,
                        backgroundColor: '#FFFFFF',
                        boxShadow: '0 2px 40px 0 rgba(59,64,83,0.30)',
                        borderRadius: 4 * this.relativeRate,
                        textStyle: {
                            color: '#3B4053',
                            fontSize: 20 * this.relativeRate,
                        },
                        formatter: '{b}<br/>{c}%',
                        extraCssText: 'box-shadow: 0 2px 40px 0 rgba(59,64,83,0.30)'
                    },
                    series: [{
                        type: "pie",
                        name: "",
                        legendHoverLink: true,
                        hoverAnimation: true,
                        minAngle: 20,
                        z: 10,
                        center: ['50%', '60%'],
                        radius: [70 * this.relativeRate, 160 * this.relativeRate],
                        itemStyle: {
                            normal: {
                                borderWidth: 10.5 * this.relativeRate,
                                borderColor: '#FFFFFF',
                            }
                        },
                        label: {
                            normal: {
                                show: true,
                                position: "inside",
                                formatter: x => {
                                    var resultName = x.name;
                                    var reg = this.regval.exec(resultName);
                                    if (reg) {
                                        resultName = resultName.replace(reg[0], "");
                                        if (resultName.length > 3) {
                                            resultName = resultName.split("");
                                            resultName.splice(3, 0, reg[0] + "\n");
                                            resultName = resultName.join("");
                                            return resultName
                                        } else {
                                            resultName = resultName.split("");
                                            resultName.splice(3, 0, "\n" + reg[0]);
                                            resultName = resultName.join("");
                                            return resultName
                                        }

                                    }
                                    if (resultName.length > 2) {
                                        resultName = resultName.split("");
                                        resultName.splice(2, 0, "\n");
                                        resultName = resultName.join("");
                                    }
                                    return resultName;
                                },
                                color: '#FFF',
                                fontSize: 24 * this.relativeRate,
                                rotate: 90,
                            }
                        },
                        data: seriesData
                    }, {
                        type: "pie",
                        name: "",
                        z: 1,
                        minAngle: 20,
                        silent: true,
                        center: ['50%', '60%'],
                        radius: [0 * this.relativeRate, 70 * this.relativeRate],
                        itemStyle: {
                            normal: {
                                color: 'rgba(255,255,255,0)',
                                borderColor: 'rgba(59, 64, 83, 0.1)',
                                borderType: 'dashed'
                            },
                        },
                        label: {
                            normal: {
                                show: true,
                                position: "inside",
                                formatter: '{c}%',
                                color: ' #3B4053',
                                fontSize: 15 * this.relativeRate,
                                rotate: 190,
                            }, emphasis: {
                                show: false
                            }
                        },
                        data: assistData
                    }]
                };
                var ele = document.getElementById(element);
                ele.style.width = window.innerWidth + 'px';
                var chart = this.echarts.init(ele);
                chart.setOption(option);
            },
            getNewList(list,list2,pieColor){
                var newList=[];
                for(var i=0;i<list.length;i++){
                    if(parseFloat(list[i].proportion)>0){
                        list2.push({
                            name: list[i].name,
                            value: list[i].proportion,
                        });
                        var obj= {
                            name: list[i].name,
                            value: list[i].proportion,
                            itemStyle: {
                                normal: {
                                    color: new this.echarts.graphic.LinearGradient(0, 0, 0, 1, [{
                                        offset: 0,
                                        color: pieColor[i].start
                                    }, {
                                        offset: 1,
                                        color: pieColor[i].end
                                    }], false),
                                },
                            }
                        }
                        newList.push(obj);
                    }
                }
                return newList;
            },
            showCompany() {
                this.tempCompany = this.currentCompany;
                if (this.companys.length == 0) {
                    axios.get('../static/json/companylist.json').then(res => {
                        this.companys = res.data.companyList;
                    })
                }
                this.popupVisible = true;
            },
            confirmCompany: function () {
                this.popupVisible = false;
                this.currentCompany = this.tempCompany;
                this.drawRectification();
            },
            confirmYear: function () {
                this.popupYear = false;
                this.currentYear = this.tempYear;
                this.currentQuarter = this.tempQuarter;
                this.drawRectification();
            },
            showYear() {
                if (this.yearList.length == 0) {
                    for (var i = this.startYear; i <= this.endYear; i++) {
                        this.yearList.push(i + "年")
                    }
                }
                this.slots[0].values = this.yearList;
                this.popupYear = true;
            },
            btnSelect(company) {
                this.tempCompany = company
            },
            cancel() {
                this.popupVisible = false;
                this.popupYear = false;
            },
            onValuesChange(picker, values) {
                if (values[0]) {
                    this.tempYear = values[0].substring(0, 4);
                }
                var quarter = values[1].substring(1, 2);
                this.tempQuarter = (quarter == '一' ? 1 : quarter == '二' ? 2 : quarter == '三' ? 3 : 4);
            }
        }
    }
</script>

<style scoped>
    .mint-header.is-fixed {
        height: 1rem;
        width: 100%;
        background: #fff;
        color: #116DCF;
        box-shadow: 0 0 0.28rem 0px rgba(233, 233, 233, 0.50);
        border: 0;
    }

    .mint-header >>> .mint-button {
        height: 100%;
    }

    .mint-header >>> .mint-header-title {
        font-size: 0.32rem;
        color: #0A0A0A;
        letter-spacing: 0.015rem;
    }

    .mint-tabbar.is-fixed {
        padding: 0 0.15rem;
        background: #FFFFFF;
        box-shadow: 0 0 0.28rem 0 rgba(233, 233, 233, 0.50);
    }

    .header-img {
        width: 0.84rem;
        height: 0.84rem;
    }

    .spliter {
        width: 100%;
        height: 0.2rem;
        background: #eee;
    }

    .has-header {
        margin-top: 1rem;
    }

    .part-title {
        margin: 0.5rem 0;
        height: 0.45rem;
        line-height: 0.45rem;
        font-size: 0.32rem;
        color: #3B4053;
        letter-spacing: 0.02rem;
    }
    .part-title>span:first-child {
        display: inline-block;
        height: 0.38rem;
        width: 0.05rem;
        vertical-align: text-bottom;
        margin-left: 0.3rem;
        margin-right: 0.17rem;
        background-image: -webkit-gradient(linear, left top, left bottom, from(#116DCF), to(#9DDBFF));
        background-image: linear-gradient(-180deg, #116DCF 0%, #9DDBFF 100%);
        border-radius: 1rem;
    }
    .ratio-lr-time{
        position: absolute;
        z-index: 1;
        width: 100%;
        top: 2.4rem;
        font-size: 0.26rem;
        color: #3B4053;
        letter-spacing: 0;
    }
    .ratio-lr-time>span:first-child{
        width: 2.8rem;
        display: inline-block;
        text-align: right;
    }
    .ratio-lr-time>span:last-child{
        display: inline-block;
        margin-left: 1.84rem;
    }
    .ratiochart{
        width:100%;
        height:4rem;
        margin-top: 0.5rem
    }

    .rowbox{
        display: flex;
        width: 100%;
        overflow: hidden;
    }
    .rowbox > .collectpie {
        height: 4.5rem;
        flex-basis:50%;
        padding: 0.1rem 0
    }
    /*底部弹框*/
    .popup {
        width: 100%;
        height: 40%;
    }

    .select-title {
        position: absolute;
        top: 0;
        left: 0;
        height: 1rem;
        right: 0;
        margin: 0 0.3rem;
        border-bottom: 1px solid #ECECEC;
        line-height: 1rem;
        text-align: center;
        font-size: 0.32rem;
        color: #3B4053;
        letter-spacing: 0;
    }

    .select-title > .cancel {
        opacity: 0.7;
        font-size: 0.28rem;
        color: #3B4053;
        float: left;
    }

    .select-title > .confirm {
        opacity: 0.7;
        font-size: 0.28rem;
        color: #116DCF;
        float: right;
    }

    .spliter {
        width: 100%;
        height: 0.2rem;
        background: #eee;
    }

    /*机构时间查询样式*/
    .popup {
        width: 100%;
        height: 50%;
    }

    .select-title {
        position: absolute;
        top: 0;
        left: 0;
        height: 1rem;
        right: 0;
        margin: 0 0.3rem;
        border-bottom: 1px solid #ECECEC;
        line-height: 1rem;
        text-align: center;
        font-size: 0.32rem;
        color: #3B4053;
        letter-spacing: 0;
        box-sizing: border-box;
    }

    .select-title > .cancel {
        opacity: 0.7;
        font-size: 0.28rem;
        color: #3B4053;
        float: left;
    }

    .select-title > .confirm {
        opacity: 0.7;
        font-size: 0.28rem;
        color: #116DCF;
        float: right;
    }

    .select-company {
        text-align: center;
        margin-top: 1rem;
        height: 100%;
        overflow-y: scroll;
        -webkit-overflow-scrolling: touch;
    }

    .select-company > button {
        display: inline-block;
        background: transparent;
        float: left;
        height: 0.6rem;
        line-height: 0.6rem;
        width: 2rem;
        border-radius: 1rem;
        margin: 0.05rem 0;
        border: 0.01rem solid transparent;
        box-sizing: border-box;
    }

    .select-company > button.selected {
        color: #116dfc;
        border: 0.01rem solid #116dfc
    }

    .select-company > button:nth-child(even) {
        margin-left: 0.62rem;
    }

    .select-company > button:first-child {
        margin-right: 5.48rem;
        margin-left: 0.62rem;
        margin-top: 0.26rem;
    }

    .select-company > button:nth-child(2n+3) {
        margin-right: 1.56rem;
        margin-left: 1.26rem;
    }

    .blank-fill {
        display: inline-block;
        width: 100%;
        height: 1rem;
    }

    .company-show {
        display: inline-block;
        border: 1px solid #116DCF;
        border-radius: 0.3rem;
        width: 3rem;
        height: 0.5rem;
        line-height: 0.5rem;
        text-align: center;
        color: #116DCF;
        margin: 0.4rem 0 0.4rem 0.35rem;
    }

    .company-show:after {
        content: " ";
        display: inline-block;
        line-height: 0.113rem;
        height: 0.113rem;
        width: 0.113rem;
        border-bottom: 1px solid #116DCF;
        border-right: 1px solid #116dcf;
        transform: rotate(45deg);
        -webkit-transform: rotate(45deg);
        -moz-transform: rotate(45deg);
        -o-transform: rotate(45deg);
        -ms-transform: rotate(45deg);
        transform: rotate(45deg);
        transform: rotate(45deg);
        position: relative;
        top: -0.07rem;
        margin-left: 0.14rem;
    }

    .year-show {
        display: inline-block;
        border: 1px solid #116DCF;
        border-radius: 0.3rem;
        width: 3rem;
        height: 0.5rem;
        line-height: 0.5rem;
        text-align: center;
        color: #116DCF;
        margin: 0.4rem 0 0.4rem 0.35rem;
    }

    .year-show:after {
        content: " ";
        display: inline-block;
        line-height: 0.113rem;
        height: 0.113rem;
        width: 0.113rem;
        border-bottom: 1px solid #116DCF;
        border-right: 1px solid #116dcf;
        transform: rotate(45deg);
        -webkit-transform: rotate(45deg);
        -moz-transform: rotate(45deg);
        -o-transform: rotate(45deg);
        -ms-transform: rotate(45deg);
        transform: rotate(45deg);
        transform: rotate(45deg);
        position: relative;
        top: -0.07rem;
        margin-left: 0.14rem;
    }
</style>



