<template>
    <div>
        <mt-header title="地图展示" :fixed="true">
            <router-link to="/" slot="left">
                <mt-button>
                    <img class="header-img" src="static/image/ic_navbar_back@2x.png">
                </mt-button>
            </router-link>
            <router-link to="/" slot="right">
                <mt-button>
                    <img class="header-img" src="static/image/ic_navbar_home@2x.png">
                </mt-button>
            </router-link>
        </mt-header>
        <div class="has-header ">
            <div class="spliter"></div>
            <mt-navbar v-model="tabtype">
                <mt-tab-item id="losstable" class="left-tab tab-one">报表展示</mt-tab-item>
                <mt-tab-item id="lossmap" class="right-tab tab-one">地图展示</mt-tab-item>
            </mt-navbar>
            <mt-tab-container v-model="tabtype" swipeable>
                <mt-tab-container-item id="losstable">
                    <div class="time_last">
                        <span>{{currentYear}}年第{{currentQuarter==1?'一':currentQuarter==2?'二':currentQuarter==3?'三':'四'}}季度</span>
                    </div>
                    <div>
                        <div class="table-noscroll"
                             @touchstart.stop
                             @mousedown.stop
                             @touchmove.stop
                             @mousemove.stop
                             @mouseleave.stop
                             @touchend.stop>
                            <div class="table-content-line">
                                <span class="table-title-type">发生机构</span>
                                <span class="table-title-type">事件个数</span>
                                <span class="table-title-type">损失金额（万元）</span>
                            </div>
                            <div class="table-content-line even-odd" v-for="item in allData.initData">
                                <span class="table-content-value">{{item.institution}}</span>
                                <span class="table-content-value">{{item.lossEvents}}</span>
                                <span class="table-content-value">{{item.lossMoney}}</span>
                            </div>
                        </div>
                    </div>
                </mt-tab-container-item>
                <mt-tab-container-item id="lossmap">
                    <div class="time_last">
                       <span>{{currentYear}}年第{{currentQuarter==1?'一':currentQuarter==2?'二':currentQuarter==3?'三':'四'}}季度</span>
                    </div>
                    <div class="part-title">
                        <span></span>损失事件分布
                        <span>单位:件</span>
                    </div>
                    <div id="losseventsmap" class="map"></div>
                    <div class="part-title">
                        <span></span>损失金额分布
                        <span>单位:万元</span>
                    </div>
                    <div id="lossmoneymap" class="map"></div>
                </mt-tab-container-item>
            </mt-tab-container>
            <div style="height:1.4rem"></div>
        </div>
        <mt-tabbar :fixed="true" class="columnbox">
            <div class="rowbox">
                <span @click="showYear" class="item-show">查询时间</span>
                <span @click="showEventType" class="item-show">{{currentEventType.code?currentEventType.name.substr(0,5)+'...':'一级事件类型'}}</span>
            </div>
            <div  class="rowbox">
                <span @click="showBusiness" class="item-show">{{currentBusiness.code?currentBusiness.name:'业务条线'}}</span>
                <span @click="showRiseReason" class="item-show">{{currentRiseReason.code?currentRiseReason.name:'一级风险成因'}}</span>
            </div>
        </mt-tabbar>
        <!--一级事件类型弹窗-->
        <mt-popup
                v-model="popupVisible"
                class="popup"
                position="bottom">
            <div class="select-title">
                <span class="cancel" @click="cancel">取消</span>
                <span>一级事件类型</span>
                <span class="confirm" @click="confirmEventType">确认</span>
            </div>
            <div class="select-company">
                <button v-for="item in eventTypeList" @click="btnSelect('eventType',item)"
                        :class="[item.code==tempEventType.code?'selected':'']">{{item.name}}
                </button>
                <div class="blank-fill"></div>
            </div>
        </mt-popup>
        <mt-popup
                v-model="popupBusiness"
                class="popup"
                position="bottom">
            <div class="select-title">
                <span class="cancel" @click="cancel">取消</span>
                <span>业务条线</span>
                <span class="confirm" @click="confirmBusiness">确认</span>
            </div>
            <div class="select-company" style="margin-top: 1.2rem">
                <button v-for="item in businessList" @click="btnSelect('business',item)"
                        :class="[item.code==tempBusiness.code?'selected':'']" style="height: 0.6rem;">{{item.name}}
                </button>
                <div class="blank-fill"></div>
            </div>
        </mt-popup>
        <mt-popup
                v-model="popupRiseReason"
                class="popup"
                position="bottom">
            <div class="select-title">
                <span class="cancel" @click="cancel">取消</span>
                <span>一级风险成因</span>
                <span class="confirm" @click="confirmRiseReason">确认</span>
            </div>
            <div class="select-company">
                <button v-for="item in riseReasonList" @click="btnSelect('riseReason',item)"
                        :class="[item.code==tempRiseReason.code?'selected':'']">{{item.name}}
                </button>
                <div class="blank-fill"></div>
            </div>
        </mt-popup>
        <!--年份选择弹窗-->
        <mt-popup
                v-model="popupYear"
                class="popup"
                position="bottom">
            <div class="select-title">
                <span class="cancel" @click="cancel">取消</span>
                <span>选择时间</span>
                <span class="confirm" @click="confirmYear">确认</span>
            </div>
            <mt-picker :slots="slots" @change="onValuesChange"></mt-picker>
        </mt-popup>
    </div>
</template>

<script>
    import axios from 'axios'
    import '../../static/js/china'
    export default {
        data() {
            return {
                tabtype: 'losstable',
                regval: /\d+/,
                popupVisible: false,     //机构选择弹框
                popupYear: false,        //年份选择弹窗
                popupBusiness:false,
                popupRiseReason:false,
                eventTypeList: [],             //事件类型数组
                businessList: [],
                riseReasonList: [],
                startYear: 2015,
                endYear: 2035,
                yearList: [],
                tempEventType: {},
                tempBusiness: {},
                tempRiseReason: {},
                currentEventType: {},
                currentBusiness: {},
                currentRiseReason: {},
                currentYear: '',
                tempYear: '',
                currentQuarter: '',
                tempQuarter: '',
                pieColor: [
                    {
                        start: '#0044BB',
                        end: '#204BEF'
                    },{
                        start: '#3FEE91',
                        end: '#86EACD'
                    },{
                        start: '#3CA9F1',
                        end: '#1188D7'
                    }
                ],
                allData:{
                    initData: [],
                    lossEvents:[],
                    lossMoney:[]
                },
                eventColor:[
                    // "rgba(153,209,244,1)", "rgba(195,236,251,1)", "rgba(213,248,253,1)"
                    '#116DCF','#2196F3','#68CBF8','#7FDDE9'
                ],
                moneyColor:[
                    // "rgba(153,209,244,1)", "rgba(195,236,251,1)", "rgba(213,248,253,1)"
                    '#116DCF','#2196F3','#68CBF8','#7FDDE9'
                ],
                eventLevel:[
                    // {start: 50, end: 300}, {start: 10, end: 50},{start: 0, end: 10}
                    {gte: 101,label:"100件以上"}, // 不指定 max，表示 max 为无限大（Infinity）。
                    {gte: 50, lte: 100,label:"100件以下"},
                    {gte: 10, lte: 49,label:"50件以下"},
                    {lte: 9,label:"10件以下"}
                ],
                moneyLevel:[
                    // {start: 10, end: 100}, {start: 1, end: 10},{start: 0, end: 1}
                    {gte: 51,label:"50万以上"}, // 不指定 max，表示 max 为无限大（Infinity）。
                    {gte: 11, lte: 50,label:"50万以下"},
                    {gte: 1, lte: 10,label:"10万以下"},
                    {lte: 1,label:"1万以下"}
                ],
                slots: [
                    {
                        flex: 1,
                        values: [],
                        className: 'slot1',
                        textAlign: 'right'
                    }, {
                        divider: true,
                        content: '-',
                        className: 'slot2'
                    }, {
                        flex: 1,
                        values: ["第一季度", "第二季度", "第三季度", "第四季度"],
                        className: 'slot4',
                        textAlign: 'left'
                    }
                ]

            }
        },
        watch:{
            tabtype: function (val, oldVal) {
                if (val == 'lossmap') {
                    this.drawMap(this.allData.lossEvents,'losseventsmap');
                    this.drawMap(this.allData.lossMoney,'lossmoneymap');
                }
            }
        },
        created() {
            var date = new Date;
            this.currentYear = date.getFullYear();
            this.currentQuarter = Math.ceil((date.getMonth() + 1) / 3);
            this.init();
        },
        methods: {
            init(){
                axios.get('../static/json/MapShow.json').then(res => {
                    this.allData=res.data.allData;
                    this.drawMap(this.allData.lossEvents,'losseventsmap');
                    this.drawMap(this.allData.lossMoney,'lossmoneymap');
                });
            },
            drawMap(data,id){
                var splitList=[];
                var colorList=[];
                var name="";
                if('losseventsmap'==id){
                    splitList=this.eventLevel;
                    colorList=this.eventColor;
                    name="损失事件个数";
                }
                if('lossmoneymap'==id){
                    splitList=this.moneyLevel;
                    colorList=this.moneyColor;
                    name="损失金额";
                }
                var optionMap = {
                    backgroundColor: '#FFFFFF',
                    title: {
                        text: '',
                        subtext: '',
                        x:'left',
                        y:'bottom'
                    },
                    tooltip : {
                        trigger: 'item',
                        formatter:param=>{
                            if(isNaN(param.value)){
                                return name+'<br/>'+param.name+"：该地区无数据"
                            }else{
                                return name+'<br/>'+param.name+"："+param.value
                            }
                        }
                    },
                    //左侧小导航图标
                    visualMap: {
                        show: true,
                        x: 'left',
                        y: 'bottom',
                        // splitList: splitList,
                        color: colorList,
                        orient:'horizontal',
                        pieces:splitList,
                    },
                    series: [
                       {
                           name:name,
                          type: 'map',
                          mapType: 'china',
                          roam: false,
                          zoom: 1.2,
                          label: {
                              normal: {
                                  areaColor:"#E3E3E4",
                                  color:"#E3E3E4",
                                  borderColor:"#FFF",
                                  show:false
                              },
                              emphasis: {
                                  label: {
                                      show: true
                                  },
                                  areaColor:"#EF731E"
                              }
                          },
                          itemStyle:{
                              borderColor:'#bbb'
                          },
                          data:data  //数据
                       }
                    ]
                };
                var ele=document.getElementById(id);
                var myChart=this.echarts.init(ele);
                myChart.setOption(optionMap);
            },
            showEventType() {
                this.tempEventType = this.currentEventType;
                if (this.eventTypeList.length == 0) {
                    axios.get('../static/json/companylist.json').then(res => {
                        this.eventTypeList = res.data.eventTypeList;
                    })
                }
                this.popupVisible = true;
            },
            confirmEventType: function () {
                this.popupVisible = false;
                this.currentEventType = this.tempEventType;
                this.init();
            },
            showBusiness(){
                this.tempBusiness = this.currentBusiness;
                if (this.businessList.length == 0) {
                    axios.get('../static/json/companylist.json').then(res => {
                        this.businessList = res.data.businessList;
                    })
                }
                this.popupBusiness = true;
            },
            confirmBusiness: function () {
                this.popupBusiness = false;
                this.currentBusiness = this.tempBusiness;
                this.init();
            },
            showRiseReason(){
                this.tempRiseReason = this.currentRiseReason;
                if (this.riseReasonList.length == 0) {
                    axios.get('../static/json/companylist.json').then(res => {
                        this.riseReasonList = res.data.riseReasonList;
                    })
                }
                this.popupRiseReason = true;
            },
            confirmRiseReason: function () {
                this.popupRiseReason = false;
                this.currentRiseReason= this.tempRiseReason;
                this.init();
            },
            confirmYear: function () {
                this.popupYear = false;
                this.currentYear = this.tempYear;
                this.currentQuarter = this.tempQuarter;
                this.init();
            },
            showYear() {
                if (this.yearList.length == 0) {
                    for (var i = this.startYear; i <= this.endYear; i++) {
                        this.yearList.push(i + "年")
                    }
                }
                this.slots[0].values = this.yearList;
                this.popupYear = true;
            },
            btnSelect(type,item) {
                if('eventType'==type){
                    this.tempEventType = item
                }else if('business'==type){
                    this.tempBusiness=item;
                }else if('riseReason'){
                    this.tempRiseReason=item;
                }
            },
            cancel() {
                this.popupVisible = false;
                this.popupYear = false;
                this.popupBusiness = false;
                this.popupRiseReason = false;
            },
            onValuesChange(picker, values) {
                if (values[0]) {
                    this.tempYear = values[0].substring(0, 4);
                }
                var quarter = values[1].substring(1, 2);
                this.tempQuarter = (quarter == '一' ? 1 : quarter == '二' ? 2 : quarter == '三' ? 3 : 4);
            }
        }
    }
</script>

<style scoped>
    .mint-header.is-fixed {
        height: 1rem;
        width: 100%;
        background: #fff;
        color: #116DCF;
        box-shadow: 0 0 0.28rem 0px rgba(233, 233, 233, 0.50);
        border: 0;
    }

    .mint-header >>> .mint-button {
        height: 100%;
    }

    .mint-header >>> .mint-header-title {
        font-size: 0.32rem;
        color: #0A0A0A;
        letter-spacing: 0.015rem;
    }

    .mint-tabbar.is-fixed {
        padding: 0 0.15rem;
        background: #FFFFFF;
        box-shadow: 0 0 0.28rem 0 rgba(233, 233, 233, 0.50);
    }

    .header-img {
        width: 0.84rem;
        height: 0.84rem;
    }

    .spliter {
        width: 100%;
        height: 0.2rem;
        background: #eee;
    }

    .has-header {
        margin-top: 1rem;
    }
    .part-title {
        margin: 0.5rem 0;
        height: 0.45rem;
        line-height: 0.45rem;
        font-size: 0.32rem;
        color: #3B4053;
        letter-spacing: 0.02rem;
    }
    .part-title>span:first-child {
        display: inline-block;
        height: 0.38rem;
        width: 0.05rem;
        vertical-align: text-bottom;
        margin-left: 0.3rem;
        margin-right: 0.17rem;
        background-image: -webkit-gradient(linear, left top, left bottom, from(#116DCF), to(#9DDBFF));
        background-image: linear-gradient(-180deg, #116DCF 0%, #9DDBFF 100%);
        border-radius: 1rem;
    }
    .part-title>span:last-child{
        float: right;
        display: inline-block;
        opacity: 0.3;
        font-family: PingFangSC-Regular;
        font-size: 0.24rem;
        color: #3B4053;
        letter-spacing: 0;
        margin-right: 0.3rem;
    }
    .time_last {
        text-align: right;
        opacity: 0.5;
        font-family: PingFangSC-Regular;
        font-size: .3rem;
        color: #0a0a0a;
        letter-spacing: 0;
        margin: .2rem .3rem;
    }
    .table-noscroll {
        display: inline-block;
        width: 7.5rem;
        overflow-x: hidden;
        white-space: nowrap;
        margin-top: 0.3rem;
        margin-left: 0.1rem;
    }


    .table-fixed > .even-odd:nth-child(even), .table-noscroll > .even-odd:nth-child(even), .table-scroll > .even-odd:nth-child(even) {
        background: rgba(236, 236, 236, 0.3);
    }


    /*
  *切换tab
  */
    .mint-tab-item.left-tab {
        margin-right: 0.32rem;
        margin-left: 0.63rem;
        padding: 0;
    }

    .mint-tab-item.right-tab {
        margin-right: 0.63rem;
        margin-left: 0.32rem;
        padding: 0;
    }
    .mint-navbar {
        height: 0.8rem;
        font-family: PingFangSC-Regular;
        margin: 0.1rem 0.3rem 0.04rem;
        color: rgba(38, 162, 255, 0.3);
        letter-spacing: 0;
    }

    .mint-navbar>>> .mint-tab-item.is-selected {
        border-bottom: 0.04rem solid #116DCF;
        letter-spacing: 0;
        color: rgba(38, 162, 255);
    }

    .tab-one>>>.mint-tab-item-label{
        font-size: 0.32rem;
        line-height: 0.8rem;
        padding: 0;
    }

    /*滑动表格*/
    .table-title-type {
        height: 0.8rem;
        width: 2.45rem;
        border-top: 0.01rem solid rgba(59, 64, 83, 0.10);
        border-right: 0.01rem solid rgba(59, 64, 83, 0.10);
        border-bottom: 0.01rem solid rgba(59, 64, 83, 0.10);
        text-align: center;
        display: flex;
        justify-content: center;
        align-items: center;
        box-sizing: border-box;
    }

    .table-content-value {
        height: 0.6rem;
        width: 2.45rem;
        border-bottom: 0.01rem solid rgba(59, 64, 83, 0.10);
        border-right: 0.01rem solid rgba(59, 64, 83, 0.10);
        text-align: center;
        display: flex;
        justify-content: center;
        align-items: center;
        box-sizing: border-box;
    }

    .table-content-line {
        display: flex;
        width: 8rem;
    }


    /*底部弹框*/
    .popup {
        width: 100%;
        height: 40%;
    }
    .select-title {
        position: absolute;
        top: 0;
        left: 0;
        height: 1rem;
        right: 0;
        margin: 0 0.3rem;
        border-bottom: 1px solid #ECECEC;
        line-height: 1rem;
        text-align: center;
        font-size: 0.32rem;
        color: #3B4053;
        letter-spacing: 0;
    }

    .select-title > .cancel {
        opacity: 0.7;
        font-size: 0.28rem;
        color: #3B4053;
        float: left;
    }

    .select-title > .confirm {
        opacity: 0.7;
        font-size: 0.28rem;
        color: #116DCF;
        float: right;
    }

    .spliter {
        width: 100%;
        height: 0.2rem;
        background: #eee;
    }

    .map {
        width: 7.5rem;
        height: 7.05rem;
        padding: 0.1rem 0
    }

    /*时间查询样式*/
    .popup {
        width: 100%;
        height: 50%;
    }

    .picker {
        margin: 20% 0 10%;
    }
    .select-title {
        position: absolute;
        top: 0;
        left: 0;
        height: 1rem;
        right: 0;
        margin: 0 0.3rem;
        border-bottom: 1px solid #ECECEC;
        line-height: 1rem;
        text-align: center;
        font-size: 0.32rem;
        color: #3B4053;
        letter-spacing: 0;
        box-sizing: border-box;
    }

    .select-title > .cancel {
        opacity: 0.7;
        font-size: 0.28rem;
        color: #3B4053;
        float: left;
    }

    .select-title > .confirm {
        opacity: 0.7;
        font-size: 0.28rem;
        color: #116DCF;
        float: right;
    }

    .select-company {
        text-align: center;
        margin-top: 1.5rem;
        height: 100%;
        overflow-y: scroll;
        -webkit-overflow-scrolling: touch;
    }

    .select-company > button {
        display: inline-block;
        background: transparent;
        float: left;
        height: 0.8rem;
        width: 2.5rem;
        border-radius: 1rem;
        margin: 0.05rem 0;
        border: 0.01rem solid transparent;
        box-sizing: border-box;
    }

    .select-company > button.selected {
        color: #116dfc;
        border: 0.01rem solid #116dfc
    }

    .select-company > button:nth-child(even) {
        margin-left: 0.62rem;
    }

    .select-company > button:nth-child(2n+1) {
        margin-left: 1.1rem;
    }

    .blank-fill {
        display: inline-block;
        width: 100%;
        height: 1rem;
    }
    .columnbox{
        height: 1.6rem;
        display: flex;
        flex-direction: column;
    }
    .rowbox{
        display: flex;
        flex-direction: row;
    }
    .item-show {
        flex-basis: 50%;
        display: inline-block;
        border: 1px solid #116DCF;
        border-radius: 0.3rem;
        width: 3rem;
        height: 0.5rem;
        line-height: 0.5rem;
        text-align: center;
        color: #116DCF;
        margin: 0.1rem;
    }

    .item-show:after {
        content: " ";
        display: inline-block;
        line-height: 0.113rem;
        height: 0.113rem;
        width: 0.113rem;
        border-bottom: 1px solid #116DCF;
        border-right: 1px solid #116dcf;
        transform: rotate(45deg);
        -webkit-transform: rotate(45deg);
        -moz-transform: rotate(45deg);
        -o-transform: rotate(45deg);
        -ms-transform: rotate(45deg);
        transform: rotate(45deg);
        transform: rotate(45deg);
        position: relative;
        top: -0.07rem;
        margin-left: 0.14rem;
    }

</style>



