import hmacSHA1 from 'crypto-js/hmac-sha1';
import Base64 from 'crypto-js/enc-base64';
import config from './config'
import axios  from 'axios'

/*
*将o,p合并，如果p中的属性，o中有，则不覆盖
*非深度合并
*/
function merge(o,p){
    if(!o)return p;
    for(prop in p){
        if (o.hasOwnProperty[prop]) continue;
        o[prop] = p[prop]
    }
    return o;
}
function getBusserialNumber(longtime){
    var busserialNO = Math.ceil(Math.random(longtime)*10**config.requestPublic.numberDigit)+"";
    while(busserialNO.length<config.requestPublic.numberDigit){
        busserialNO = busserialNO.concat("0")
    }
    return busserialNO;
}
function getSignature(content,key){
    var _key = key || config.requestPublic.hamcKey ;
    var encryStr = hmacSHA1(content,_key);
    return Base64.stringify(encryStr)
}
export default {
   install(Vue,options){
    Vue.prototype.$hmacSHA1 = getSignature;
    /*
      * opt.name config中配置的路径名称 和url二选一，也可都填，但是url优先级高
      * opt.data 请求数据 必须
      * opt.url 如果有它，和opt.name相比。则优先使用url
      * opt.timeout 请求过期时间
      */
    Vue.prototype.$simpleHttp = function(opt){
        var _invokeTime = new Date().getTime();
        var _head = {
            "invokeTime": _invokeTime,
            "responseTime": "",
            "sourceSystem": config.requestPublic.sourceSystem,
            "busserialnumber": getBusserialNumber(_invokeTime),
            "signature": ""
        }
    	var _url = opt.url||config[opt.name];
    	var _data = opt.data;
        _data.head = merge(_data.head,_head);
        _data.head.signature = getSignature(_data.head.sourceSystem+_data.head.invokeTime);
    	var _timeout = opt.timeout||5000;
    	return new Promise(function(resolve,reject){
	        axios({
	            url:_url,
	            data:_data,
	            method:'POST',
	            timeout:_timeout,
	        }).then(function(data){
	            resolve(data.data);
	        }).catch(function(error){
	            reject(error);
	        });
      	});
    }
  }
}



