    watch: {
        active: function active(value, oldValue) {
            var that = this;
            //导航随之滚动
            var scroll_sc = document.getElementById("scroll_sc");
            var repeat_btn = document.querySelectorAll("#scroll_sc button");
            var timer;
            var mu = repeat_btn[value.slice(value.length - 1) - 1].offsetLeft - repeat_btn[0].clientWidth;
            var yuan = scroll_sc.scrollLeft;
            var x = mu - yuan;
            timer = setInterval(function () {
                if (x > 0) {
                    scroll_sc.scrollLeft += 10;
                    x -= 10;
                    if (x <= 0) clearInterval(timer);
                } else {
                    scroll_sc.scrollLeft -= 10;
                    x += 10;
                    if (x >= 0) clearInterval(timer);
                }
            }, 20);
            that.backColor = value;
        }
    },
