import Vue from 'vue'
import Router from 'vue-router'
import Index from '@/components/Index'
import CollectionAnalysis from '@/components/CollectionAnalysis'
import PointClassify from '@/components/PointClassify'
import Rectification from '@/components/Rectification'
import MapShow from '@/components/MapShow'
import RiskReport from '@/components/RiskReport'
import RCSAReport from '@/components/RCSAReport'
import RCSAControlReport from '@/components/RCSA_ControlReport'
Vue.use(Router)

export default new Router({
  routes: [
    {
      path: '/',
      name: 'index',
      component: Index,
      meta: {
      	keepAlive: false
      }
    },{
      path: '/index',
      name: 'index',
      component: Index,
      meta: {
        keepAlive: false
      }
    },{
      path:'/RiskReport',
      name:'RiskReport',
      component:RiskReport,
      meta:{
        keepAlive: false
      }
    },{
      path:'/RCSAReport',
      name:'RCSAReport',
      component:RCSAReport,
      meta:{
        keepAlive:false
      }
    },{
      path:'/RCSAControlReport',
      name:'RCSAControlReport',
      component:RCSAControlReport,
      meta:{
        keepAlive:false
      }
    },{
          path: '/CollectionAnalysis',
          name: 'CollectionAnalysis',
          component: CollectionAnalysis,
          meta: {
              keepAlive: false
          }
      },{
          path: '/PointClassify',
          name: 'PointClassify',
          component: PointClassify,
          meta: {
              keepAlive: false
          }
      },{
          path: '/Rectification',
          name: 'Rectification',
          component: Rectification,
          meta: {
              keepAlive: false
          }
      },{
          path: '/MapShow',
          name: 'MapShow',
          component: MapShow,
          meta: {
              keepAlive: false
          }
      }
  ],
  //页面跳转后滚动到最上方
  scrollBehavior (to, from, savedPosition) {
    return { x: 0, y: 0 }
  }
})





