                regval: /\d+/,
                popupVisible: false,     //机构选择弹框
                popupYear: false,        //年份选择弹窗
                companys: [],             //所有机构数组
                startYear: 2015,
                endYear: 2035,
                yearList: [],
                tempCompany: {},         //所选机构
                currentCompany: {        //当前机构
                    comCode: "01",
                    codeName: "总公司全辖"
                },
                currentYear: '',
                tempYear: '',
                currentQuarter: '',
                tempQuarter: '',
                pieColor: [
                    {
                        start:'#1470d2',
                        end:'#4c9ffc'
                    },{
                        start:'#ad90fd',
                        end:'#8365dc'
                    }, {
                        start:'#ec8f79',
                        end:'#df624c'
                    },{
                        start:'#e9bd5b',
                        end:'#ebc164'
                    }, {
                        start:'#36a997',
                        end:'#36a997'
                    },
                    {
                        start:'#1470d2',
                        end:'#4c9ffc'
                    },{
                        start:'#ad90fd',
                        end:'#8365dc'
                    }, {
                        start:'#ec8f79',
                        end:'#df624c'
                    },{
                        start:'#e9bd5b',
                        end:'#ebc164'
                    }, {
                        start:'#36a997',
                        end:'#36a997'
                    }
                ],
                allData:{
                    initData: {},           //风险成因分析数据
                    eventsData:{},          //事件分析类型
                    financeData:{},         //财务损失类型
                    nonFinanceData:{},      //非财务影响
                },
                slots: [
                    {
                        flex: 1,
                        values: [],
                        className: 'slot1',
                        textAlign: 'right'
                    }, {
                        divider: true,
                        content: '-',
                        className: 'slot2'
                    }, {
                        flex: 1,
                        values: ["第一季度", "第二季度", "第三季度", "第四季度"],
                        className: 'slot4',
                        textAlign: 'left'
                    }
                ]

            }
        },
        watch: {
            active: function (val, oldVal) {
                if (val == 'tab-container-1') {
                    this.drawPie(this.allData.initData.operationRise,'operaterisepie','操作风险事件个数占比');
                } else if (val == 'tab-container-2') {
                    this.drawPie(this.allData.eventsData.operationRise,'operaterisepie2','操作风险事件个数占比');
                    this.drawBarGraph(this.allData.eventsData.eventsTypeData,'operaterisebargraph')
                } else if (val == 'tab-container-3') {
                    this.drawLossEventsBar(this.allData.financeData.financeLoss,'losseventsbar');
                    this.drawLossEventsBar(this.allData.financeData.financeLoss,'lossmoneybar');
                    this.drawPie(this.allData.financeData.lossEvents,'losseventspie3','损失事件个数占比');
                    this.drawPie(this.allData.financeData.lossMoney,'lossmoneypie3','损失金额占比');
                } else if (val == 'tab-container-4') {
                    this.drawPie(this.allData.nonFinanceData.levelDistribution,'leveldistribution','非财务等级占比');
                    this.drawPie(this.allData.nonFinanceData.eventsProportion,'eventsproportion','其他非财务影响方面');
                }
            },
            tabtype: function (val, oldVal) {
                if (val == 'tab-pie-1') {
                    this.drawPie(this.allData.initData.operationRise,'operaterisepie','操作风险事件个数占比');
                    this.drawRose(this.allData.initData.riseReasonData,'operateriserose');
                } else if (val == 'tab-pie-2') {
                    this.drawPie(this.allData.initData.nonLossEvents,'nonlosseventspie','非损失事件个数占比');
                    this.drawRose(this.allData.initData.riseReasonData,'nonlosseventsrose');
                } else if (val == 'tab-pie-3') {
                    this.drawPie(this.allData.initData.lossEvents,'losseventspie','损失事件个数占比');
                    this.drawRose(this.allData.initData.riseReasonData,'losseventsrose');
                } else if (val == 'tab-pie-4') {
                    this.drawPie(this.allData.initData.lossMoney,'lossmoneypie','损失金额占比');
                    this.drawRose(this.allData.initData.riseReasonData,'lossmoneyrose');
                }

            },
