            drawLossEventsBar(dataAll,element){
                var val="";
                var unit = '';//单位
                var dataLeft = [];//左侧数据
                var dataRight = [];//右侧数据
                var activeData = [];//活动率数据
                var maxVal = 0;//柱状图最大值
                var val2='与往年同比变化趋势';//标题2
                var yData = [];//y轴的值
                var yDataRight = [];
                var yDataRight4 = [];
                var colors=[];
                var symbolImg=[];
                if('losseventsbar'==element){
                    val="损失事件个数";
                    unit = '(单位：件)';
                    colors=['#FAD961','#F76B1C','#F76B1C'];
                    symbolImg=['image://static/image/circle.png',40];
                    for(var i=0;i<dataAll.length;i++){
                        dataLeft.push(dataAll[i].currentLossEvents);
                        dataRight.push(dataAll[i].trendEvents);
                        yDataRight.push(dataAll[i].trendEvents);
                        if(parseInt(dataAll[i].currentLossEvents)>maxVal){
                            maxVal = parseInt(dataAll[i].currentLossEvents);
                        }
                        activeData.push(dataAll[i].currentLossEvents);
                        yDataRight4.push(dataAll[i].currentLossEvents);
                        yData.push(dataAll[i].name);
                    }
                }
                if('lossmoneybar'==element){
                    val="损失金额";
                    unit = '(单位：万元)';
                    colors=['#7DF2E3','#3869D8','#116DCF'];
                    symbolImg=['image://static/image/circle_blue.png',20];
                    for(var i=0;i<dataAll.length;i++){
                        dataLeft.push(dataAll[i].currentLossMoney);
                        dataRight.push(dataAll[i].trendMoney);
                        yDataRight.push(dataAll[i].trendMoney);
                        if(parseInt(dataAll[i].currentLossMoney)>maxVal){
                            maxVal = parseInt(dataAll[i].currentLossMoney);
                        }
                        activeData.push(dataAll[i].currentLossMoney);
                        yDataRight4.push(dataAll[i].currentLossMoney);
                        yData.push(dataAll[i].name);
                    }
                }
                var option = {
                    tooltip: {
                        show:false,
                        trigger: 'axis',
                        axisPointer: {
                            type: 'shadow'
                        },
                        formatter: function (params) {
                            return val+':'+params[0].value + '<br>增长率: '+params[1].value+'%';
                        }
                    },
                    legend: {
                        show:false,
                        //itemGap:100,
                        data: [val+unit, val2],
                        selectedMode:false
                    },
                    grid: {
                        x: 180*this.relativeRate,
                        x2:100*this.relativeRate,
                        y:10*this.relativeRate,
                        y2:10*this.relativeRate
                    },
                    xAxis: [
                        {
                            show: false,
                            type: 'value',
                            boundaryGap: [2, 0.9]
                        },{
                            show: false,
                            type: 'value',
                            boundaryGap: [4, 0]
                        }
                    ],
                    yAxis: [
                        {
                            position:'left',
                            type: 'category',
                            data: yData,
                            axisTick:{
                                show:false
                            },
                            axisLabel:{
                                margin:25*this.relativeRate,
                                textStyle:{
                                    fontSize:18*this.relativeRate,
                                    color:'#3B4053',
                                    fontWeight:'lighter'
                                }
                            },
                            axisLine:{
                                onZero:false,
                                show:false
                            }
                        },
                        {
                            // 辅助 y 轴
                            show: true,
                            axisLine:{
                                show:false,
                            },
                            axisTick:{
                                show:false
                            },
                            axisLabel:{
                                formatter:function(v){
                                    return v+'%';
                                },
                                align:'right',
                                margin:60*this.relativeRate,
                                textStyle:{
                                    fontSize:24*this.relativeRate,
                                    color:'#3B4053'
                                }
                            },
                            position:'right',
                            type: 'category',
                            data: dataRight
                        }
                    ],
                    series: [
                        {
                            // 辅助系列
                            type           : 'bar',
                            silent         : true,
                            yAxisIndex     : 1,
                            xAxisIndex     : 0,
                            itemStyle      : {
                                normal         : {
                                    barBorderRadius: 18*this.relativeRate,
                                    color: '#F7F7F8',
                                }
                            },
                            barWidth: 18*this.relativeRate,
                            data: dataLeft.map(function (val) {
                                return maxVal;
                            })
                        },
                        {
                            name: val+unit,
                            type: 'bar',
                            barWidth: 18*this.relativeRate,
                            xAxisIndex: 0,
                            data: dataLeft,
                            label: {
                                normal: {
                                    position: 'insideLeft',
                                    show: true,
                                    padding:[0,0,40*this.relativeRate,0],
                                    color:'#7C7C7C',
                                    fontSize:22*this.relativeRate,
                                }
                            },
                            itemStyle:{
                                normal:{
                                    //4个参数分别是左下右上，改变一下就可以改成颜色渐变的方向
                                    color: new this.echarts.graphic.LinearGradient(0, 0, 1, 0, [{
                                        offset: 0,
                                        color:colors[0]
                                    }, {
                                        offset: 1,
                                        color: colors[1]
                                    }]),
                                    barBorderRadius:[100*this.relativeRate, 100*this.relativeRate, 100*this.relativeRate, 100*this.relativeRate]
                                }
                            }
                        },
                        {
                            label: {
                                normal: {
                                    position: 'right',
                                    show: false,
                                    formatter: function (param) {
                                        return param.value+'%';
                                    }
                                }
                            },
                            itemStyle:{
                                normal:{
                                    color:'#3B4053 ',
                                    lineStyle:{
                                        color:colors[2]
