        height: 1.6rem;
        width: 7.2rem;
        border-top: 0.01rem solid rgba(59, 64, 83, 0.10);
        border-right: 0.01rem solid rgba(59, 64, 83, 0.10);
        border-bottom: 0.01rem solid rgba(59, 64, 83, 0.10);
        text-align: center;
        display: flex;
        flex-direction: column;
        justify-content: center;
        align-items: center;
        box-sizing: border-box;
    }
    .titletop{
        border-bottom: 0.01rem solid rgba(59,64,83,0.10);
        width: 100%;
        height: 0.6rem;
        display: flex;
        align-items: center;
        justify-content: center;
    }
    .titlebottom{
        display: flex;
        width: 100%;
        justify-content: space-around;
        height: 0.8rem;
    }
    .titleitem{
        flex-basis: 25%;
        display: flex;
        align-items: center;
        justify-content: center;
        border-right: 0.01rem solid rgba(59,64,83,0.10);
    }
    .titleitem2{
        flex-basis: 50%;
        display: flex;
        align-items: center;
        justify-content: center;
        border-right: 0.01rem solid rgba(59,64,83,0.10);
    }
    .titleitem3{
        flex-basis: 16.7%;
        display: flex;
        align-items: center;
        justify-content: center;
        border-right: 0.01rem solid rgba(59,64,83,0.10);
    }
    .table-content-name {
        height: 0.8rem;
        width: 2.2rem;
        border-bottom: 0.01rem solid rgba(59, 64, 83, 0.10);
        border-left: 0.01rem solid rgba(59, 64, 83, 0.10);
        border-right: 0.01rem solid rgba(59, 64, 83, 0.10);
        text-align: center;
        display: flex;
        justify-content: center;
        align-items: center;
        box-sizing: border-box;
    }

    .table-content-value {
        height: 0.8rem;
        width: 1.5rem;
        border-bottom: 0.01rem solid rgba(59, 64, 83, 0.10);
        border-right: 0.01rem solid rgba(59, 64, 83, 0.10);
        text-align: center;
        display: flex;
        justify-content: center;
        align-items: center;
        box-sizing: border-box;
    }

    .table-content-value2 {
        height: 0.8rem;
        width: 2rem;
        border-bottom: 0.01rem solid rgba(59, 64, 83, 0.10);
        border-right: 0.01rem solid rgba(59, 64, 83, 0.10);
        text-align: center;
        display: flex;
        justify-content: center;
        align-items: center;
        box-sizing: border-box;
    }
    .table-content-value3 {
        height: 0.8rem;
        width: 1.2rem;
        border-bottom: 0.01rem solid rgba(59, 64, 83, 0.10);
        border-right: 0.01rem solid rgba(59, 64, 83, 0.10);
        text-align: center;
        display: flex;
        justify-content: center;
        align-items: center;
        box-sizing: border-box;
    }

    .table-content-line {
        display: flex;
        width: 15rem;
    }

    .table-content-line2 {
        display: flex;
        width: 12rem;
    }
    .table-content-line3 {
        display: flex;
        width: 16.8rem;
    }
    .table-fixed {
        display: inline-block;
        width: 2.2rem;
        vertical-align: top;
        box-sizing: border-box;
    }

    .table-noscroll {
        display: inline-block;
        width: 5.1rem;
        overflow-x: hidden;
        white-space: nowrap;
    }

    .table-scroll {
        display: inline-block;
        width: 5.1rem;
        overflow-x: scroll;
        white-space: nowrap;
    }

    .table-fixed > .even-odd:nth-child(even), .table-noscroll > .even-odd:nth-child(even), .table-scroll > .even-odd:nth-child(even) {
        background: rgba(236, 236, 236, 0.3);
    }

    /*损失事件收集分析样式*/
    .time_last {
        text-align: right;
        opacity: 0.5;
        font-family: PingFangSC-Regular;
        font-size: .3rem;
        color: #0a0a0a;
        letter-spacing: 0;
        margin: .2rem .3rem;
    }

    .virgule_one_left {
        margin: 0 .66rem 0 .6rem;
        position: relative;
    }

    .virgule_one_left:after {
        content: '';
        position: absolute;
        background-image: linear-gradient(-180deg, #86EACD 0%, #3FC391 100%);
        width: .2rem;
        height: .2rem;
        top: .0825rem;
        left: -.30rem;
    }

    .virgule_two_left {
        position: relative;
    }

    .virgule_two_left:after {
        content: '';
        position: absolute;
        background-image: linear-gradient(0deg, #0F3B9C 0%, #36D1DC 100%);
        width: .2rem;
        height: .2rem;
        top: .0825rem;
        left: -.30rem;
    }

    .line_right {
        color: #C7C8CE;
        margin-right: .3rem;
    }

    .line_left {
        display: flex;
        height: .33rem;
        font-family: PingFangSC-Regular;
        font-size: .24rem;
        color: #3B4053;
        letter-spacing: 0;
        justify-content: space-between;
        margin-top: .2rem;
    }

    .collectbar {
        height: 8rem;
        width: 100%;
        padding: 0.1rem 0
    }

    .collectpie {
        height: 6rem;
        width: 100%;
        padding: 0.1rem 0
    }
    .collectpie2 {
        height: 5.5rem;
        width: 100%;
        padding: 0.1rem 0
    }
    .hotmapbottom{
        margin-top: 0.3rem;
        height:6rem;
        width: 100%;
    }

    .line_left_1{
        display: flex;
        height: .33rem;
        font-family: PingFangSC-Medium;
        opacity: 1;
        font-size: .26rem;
        color: #3B4053;
        letter-spacing: 0;
        justify-content: space-between;
        margin-top: .4rem;
    }
    .virgule_left{
        position: relative;
        padding-left: .3rem;
    }
    .line_right_1{
        color:#3B4053;
        margin-right: .3rem;
    }
    .unit{
        font-size: .24rem;
        opacity: 0.3;
        background-color: #ffffff;
    }
    /*机构时间查询样式*/
    .popup {
        width: 100%;
        height: 50%;
    }

    .select-title {
        position: absolute;
        top: 0;
        left: 0;
        height: 1rem;
        right: 0;
        margin: 0 0.3rem;
        border-bottom: 1px solid #ECECEC;
        line-height: 1rem;
        text-align: center;
        font-size: 0.32rem;
        color: #3B4053;
        letter-spacing: 0;
        box-sizing: border-box;
    }

    .select-title > .cancel {
        opacity: 0.7;
        font-size: 0.28rem;
        color: #3B4053;
        float: left;
    }

    .select-title > .confirm {
        opacity: 0.7;
        font-size: 0.28rem;
        color: #116DCF;
        float: right;
    }

    .select-company {
        text-align: center;
        margin-top: 1rem;
        height: 100%;
        overflow-y: scroll;
        -webkit-overflow-scrolling: touch;
    }

    .select-company > button {
        display: inline-block;
        background: transparent;
        float: left;
        height: 0.6rem;
        line-height: 0.6rem;
        width: 2rem;
        border-radius: 1rem;
        margin: 0.05rem 0;
        border: 0.01rem solid transparent;
        box-sizing: border-box;
    }

    .select-company > button.selected {
        color: #116dfc;
        border: 0.01rem solid #116dfc
    }

    .select-company > button:nth-child(even) {
        margin-left: 0.62rem;
    }

    .select-company > button:first-child {
        margin-right: 5.48rem;
        margin-left: 0.62rem;
        margin-top: 0.26rem;
    }

    .select-company > button:nth-child(2n+3) {
        margin-right: 1.56rem;
        margin-left: 1.26rem;
    }

    .blank-fill {
        display: inline-block;
        width: 100%;
        height: 1rem;
    }

    .company-show {
        display: inline-block;
        border: 1px solid #116DCF;
        border-radius: 0.3rem;
        width: 3rem;
        height: 0.5rem;
        line-height: 0.5rem;
        text-align: center;
        color: #116DCF;
        margin: 0.4rem 0 0.4rem 0.35rem;
    }

    .company-show:after {
        content: " ";
        display: inline-block;
        line-height: 0.113rem;
        height: 0.113rem;
        width: 0.113rem;
        border-bottom: 1px solid #116DCF;
        border-right: 1px solid #116dcf;
        transform: rotate(45deg);
        -webkit-transform: rotate(45deg);
        -moz-transform: rotate(45deg);
        -o-transform: rotate(45deg);
        -ms-transform: rotate(45deg);
        transform: rotate(45deg);
        transform: rotate(45deg);
        position: relative;
        top: -0.07rem;
        margin-left: 0.14rem;
    }

    .year-show {
        display: inline-block;
        border: 1px solid #116DCF;
        border-radius: 0.3rem;
        width: 3rem;
        height: 0.5rem;
        line-height: 0.5rem;
        text-align: center;
        color: #116DCF;
        margin: 0.4rem 0 0.4rem 0.35rem;
    }

    .year-show:after {
        content: " ";
        display: inline-block;
        line-height: 0.113rem;
        height: 0.113rem;
        width: 0.113rem;
        border-bottom: 1px solid #116DCF;
        border-right: 1px solid #116dcf;
        transform: rotate(45deg);
        -webkit-transform: rotate(45deg);
        -moz-transform: rotate(45deg);
        -o-transform: rotate(45deg);
        -ms-transform: rotate(45deg);
        transform: rotate(45deg);
        transform: rotate(45deg);
        position: relative;
        top: -0.07rem;
        margin-left: 0.14rem;
    }

    .part-title {
        margin-top: 0.5rem;
        height: 0.45rem;
        line-height: 0.45rem;
        font-size: 0.32rem;
        color: #3B4053;
        letter-spacing: 0.02rem;
    }
    .part-title>span:first-child {
        display: inline-block;
        height: 0.38rem;
        width: 0.05rem;
        vertical-align: text-bottom;
        margin-left: 0.3rem;
        margin-right: 0.17rem;
        background-image: -webkit-gradient(linear, left top, left bottom, from(#116DCF), to(#9DDBFF));
        background-image: linear-gradient(-180deg, #116DCF 0%, #9DDBFF 100%);
        border-radius: 1rem;
    }
</style>



