<template>
    <div>
        <mt-header title="损失事件收集分析" :fixed="true">
            <router-link to="/" slot="left">
                <mt-button>
                    <img class="header-img" src="static/image/ic_navbar_back@2x.png">
                </mt-button>
            </router-link>
            <router-link to="/" slot="right">
                <mt-button>
                    <img class="header-img" src="static/image/ic_navbar_home@2x.png">
                </mt-button>
            </router-link>
        </mt-header>
        <div class="has-header ">
            <div class="spliter"></div>
            <mt-navbar v-model="tabtype">
                <mt-tab-item id="overallsituation" class="left-tab tab-one">整体状况</mt-tab-item>
                <mt-tab-item id="information" class="right-tab tab-one">信息汇总</mt-tab-item>
            </mt-navbar>
            <mt-tab-container v-model="tabtype" :swipeable="true">
                <mt-tab-container-item id="overallsituation">
                    <div class="time_last"><span style="float: left;">{{currentCompany.codeName}}</span><span>{{currentYear}}年第{{currentQuarter==1?'一':currentQuarter==2?'二':currentQuarter==3?'三':'四'}}季度</span>
                    </div>
                    <div class="part-title">
                        <span></span>各业务条线的操作风险事件数
                    </div>
                    <div class="line_left">
                        <div>
                            <span class="virgule_one_left">损失事件</span>
                            <span class="virgule_two_left">非损失事件</span>
                        </div>
                        <div class="line_right">单位：件</div>
                    </div>
                    <div id="riseevent" class="collectbar"></div>
                    <div class="part-title">
                        <span></span>各业务条线的损失金额占比
                    </div>
                    <div id="risepie" class="collectpie"></div>
                    <div>
                        <div class="table-fixed ">
                            <span class="table-title-name"></span>
                            <span class="table-content-name even-odd" v-for="item in allData.initData.riseCollectInfo">{{item.name}}</span>
                        </div>
                        <div class="table-noscroll"
                             @touchstart.stop
                             @mousedown.stop
                             @touchmove.stop
                             @mousemove.stop
                             @mouseleave.stop
                             @touchend.stop>
                            <div class="table-content-line">
                                <span class="table-title-type">本期情况</span>
                                <span class="table-title-type">往年同期情况</span>
                                <span class="table-title-type">同比变化趋势<br/>（合计）</span>
                            </div>
                            <div class="table-content-line even-odd" v-for="item in allData.initData.riseCollectInfo">
                                <span class="table-content-value">{{item.current}}</span>
                                <span class="table-content-value">{{item.last}}</span>
                                <span class="table-content-value">{{item.trend}}</span>
                            </div>
                        </div>
                    </div>
                </mt-tab-container-item>
                <mt-tab-container-item id="information">
                    <div class="time_last">
                        <span style="float: left;">{{currentCompany.codeName}}</span><span>{{currentYear}}年第{{currentQuarter==1?'一':currentQuarter==2?'二':currentQuarter==3?'三':'四'}}季度</span>
                    </div>
                    <div class="part-title2">
                        <span></span>公司重大操作风险事件信息汇总表
                    </div>
                    <div>
                        <div class="table-scroll"
                             @touchstart.stop
                             @mousedown.stop
                             @touchmove.stop
                             @mousemove.stop
                             @mouseleave.stop
                             @touchend.stop>
                            <div class="table-content-line2">
                                <span class="table-title-type2">发生机构</span>
                                <span class="table-title-type2">发生日期</span>
                                <span class="table-title-type2">发现日期</span>
                                <span class="table-title-type2">发现途径</span>
                                <span class="table-title-type2">风险成因</span>
                                <span class="table-title-type2">损失总额</span>
                                <span class="table-title-type2">非财务影响等级</span>
                            </div>
                            <div class="table-content-line2 even-odd" v-for="item in allData.infoData.lossEventsDetail">
                                <span class="table-content-value2">{{item.happenInstitution}}</span>
                                <span class="table-content-value2">{{item.happenDate}}</span>
                                <span class="table-content-value2">{{item.foundDate}}</span>
                                <span class="table-content-value2">{{item.foundWay}}</span>
                                <span class="table-content-value2">{{item.riskReason}}</span>
                                <span class="table-content-value2">{{item.totalLoss}}</span>
                                <span class="table-content-value2">{{item.effectLevel}}</span>
                            </div>
                        </div>
                    </div>
                    <div class="part-title2">
                        <span></span>公司损失总额前五名的操作风险事件信息汇总表
                    </div>
                    <div>
                        <div class="table-scroll"
                             @touchstart.stop
                             @mousedown.stop
                             @touchmove.stop
                             @mousemove.stop
                             @mouseleave.stop
                             @touchend.stop>
                            <div class="table-content-line2">
                                <span class="table-title-type2">发生机构</span>
                                <span class="table-title-type2">发生日期</span>
                                <span class="table-title-type2">发现日期</span>
                                <span class="table-title-type2">发现途径</span>
                                <span class="table-title-type2">风险成因</span>
                                <span class="table-title-type2">损失总额</span>
                                <span class="table-title-type2">非财务影响等级</span>
                            </div>
                            <div class="table-content-line2 even-odd" v-for="item in allData.infoData.lossMoneyDetail">
                                <span class="table-content-value2">{{item.happenInstitution}}</span>
                                <span class="table-content-value2">{{item.happenDate}}</span>
                                <span class="table-content-value2">{{item.foundDate}}</span>
                                <span class="table-content-value2">{{item.foundWay}}</span>
                                <span class="table-content-value2">{{item.riskReason}}</span>
                                <span class="table-content-value2">{{item.totalLoss}}</span>
                                <span class="table-content-value2">{{item.effectLevel}}</span>
                            </div>
                        </div>
                    </div>
                </mt-tab-container-item>
            </mt-tab-container>
            <div style="height:1.4rem"></div>
        </div>
        <mt-tabbar :fixed="true">
            <span @click="showCompany" class="company-show">选择机构</span>
            <span @click="showYear" class="year-show">查询时间</span>
        </mt-tabbar>
        <!--机构弹窗-->
        <mt-popup
                v-model="popupVisible"
                class="popup"
                position="bottom">
            <div class="select-title">
                <span class="cancel" @click="cancel">取消</span>
                <span>选择机构</span>
                <span class="confirm" @click="confirmCompany">确认</span>
            </div>
            <div class="select-company">
                <button v-for="company in companys" @click="btnSelect(company)"
                        :class="[company.comCode==tempCompany.comCode?'selected':'']">{{company.codeName}}
                </button>
                <div class="blank-fill"></div>
            </div>
        </mt-popup>
        <!--年份选择弹窗-->
        <mt-popup
                v-model="popupYear"
                class="popup"
                position="bottom">
            <div class="select-title">
                <span class="cancel" @click="cancel">取消</span>
                <span>选择时间</span>
                <span class="confirm" @click="confirmYear">确认</span>
            </div>
            <mt-picker :slots="slots" @change="onValuesChange"></mt-picker>
        </mt-popup>
    </div>
</template>

<script>
    import axios from 'axios'

    export default {
        data() {
            return {
                tabtype: "overallsituation",
                regval: /\d+/,
                popupVisible: false,     //机构选择弹框
                popupYear: false,        //年份选择弹窗
                companys: [],             //所有机构数组
                startYear: 2015,
                endYear: 2035,
                yearList: [],
                tempCompany: {},         //所选机构
                currentCompany: {        //当前机构
                    comCode: "01",
                    codeName: "总公司全辖"
                },
                currentYear: '',
                tempYear: '',
                currentQuarter: '',
                tempQuarter: '',
                pieColor: [
                    {
                        start:'#50A2FF',
                        end:'#116DCF'
                    },{
                        start:'#B093FF',
                        end:'#7F61D9'
                    },{
                        start:'#F09D87',
                        end:'#DD5D47'
                    },{
                        start:'#F6DF9A',
                        end:'#E6B54D'
                    },{
                        start:'#6FF5E3',
                        end:'#32C9B0'
                    },{
                        start:'#6F90A7',
                        end:'#476379'
                    }
                ],
                marketChart: null,
                allData:{
                    initData: {},           //整体状况
                    infoData: {},
                },
                slots: [
                    {
                        flex: 1,
                        values: [],
                        className: 'slot1',
                        textAlign: 'right'
                    }, {
                        divider: true,
                        content: '-',
                        className: 'slot2'
                    }, {
                        flex: 1,
                        values: ["第一季度", "第二季度", "第三季度", "第四季度"],
                        className: 'slot4',
                        textAlign: 'left'
                    }
                ]
            }
        },
        watch: {
            tabtype: function (val, oldVal) {
                if (val == 'overallsituation') {
                    this.drawBar(this.allData.initData.lossEvents);
                    this.drawPie(this.allData.initData.lossMoney);
                }
            }
        },
        created() {
            var date = new Date;
            this.currentYear = date.getFullYear();
            this.currentQuarter = Math.ceil((date.getMonth() + 1) / 3);
            this.drawCollection();
        },
        methods: {
            drawCollection() {
                axios.get('../static/json/CollectionAnalysis.json').then(res => {
                    this.allData=res.data.allData;
                    this.drawBar(this.allData.initData.lossEvents);
                    this.drawPie(this.allData.initData.lossMoney);
                })
            },
            drawBar(dataAll){
                var maxY = 500;
                var maxVal = 0;
                var vaild = [];
                var yData = [];
                var total = [];
                for (var i = 0; i < dataAll.length; i++) {
                    vaild.push(dataAll[i].loss);
                    total.push(dataAll[i].nonloss);
                    //获取最大值
                    if (dataAll[i].loss > maxVal) {
                        maxVal = dataAll[i].loss;
                    }
                    if (dataAll[i].nonloss > maxVal) {
                        maxVal = dataAll[i].nonloss;
                    }
                    yData.push(dataAll[i].name);
                }
                var option_bar = {
                    title: {
                        text: '',
                        x: 'center',
                        itemGap: 30
                    },
                    tooltip: {
                        show: false,
                        trigger: 'axis',
                        axisPointer: {
                            type: 'shadow'
                        },
                        formatter: function (params) {
                            return '损失事件:' + params[0].value + "件" + '<br>非损失事件: ' + params[1].value + "件";
                        }
                    },
                    legend: {
                        show: false,
                        data: [
                            '损失事件',
                            '非损失事件'
                        ]
                    },
                    calculable: true,
                    grid: {
                        x: 10 * this.relativeRate,
                        x2: 30 * this.relativeRate
                    },
                    xAxis: [
                        {
                            type: 'category',
                            axisTick: {
                                show: false,
                            },
                            axisLine: {show: false},
                            axisLabel: {
                                show: true,
                                interval: 0,
                                rotate: 45,
                                padding: [0, 0, 0, 28 * this.relativeRate]
                            },
                            data: yData
                        }
                    ],
                    yAxis: [
                        {
                            type: 'value',
                            show: false,
                        }
                    ],
                    series: [
                        {
                            name: '损失事件',
                            type: 'pictorialBar',
                            //symbolSize: 50,
                            label: {
                                normal: {
                                    show: true,
                                    position: 'top',
                                    formatter: function (v) {
                                        return '{top|' + v.value + '}';
                                    },
                                    rich: {
                                        top: {
                                            width: 10 * this.relativeRate,
                                            height: this.relativeRate,
                                            align: 'center',
                                            color: 'black',
                                            fontSize: 28 * this.relativeRate,
                                            fontFamily: 'Akrobat-Regular'
                                        }
                                    }
                                }
                            },
                            data: total.map(v => {
                                return {
                                    value: v,
                                    symbol: 'image://static/image/zhu.svg',
                                    symbolSize: [20 * this.relativeRate, v / maxVal * maxY * this.relativeRate]
                                }
                            })
                        }, {
                            name: '非损失事件',
                            symbolOffset: ['100%', 0],
                            type: 'pictorialBar',
                            label: {
                                normal: {
                                    show: true,
                                    position: 'top',
                                    formatter: function (v) {
                                        return '{top|' + v.value + '}';
                                    },
                                    rich: {
                                        top: {
                                            width: 50 * this.relativeRate,
                                            height: this.relativeRate,
                                            align: 'right',
                                            color: 'black',
                                            fontSize: 28 * this.relativeRate,
                                            fontFamily: 'Akrobat-Regular'
                                        }
                                    }
                                }
                            },
                            data: vaild.map(v => {
                                return {
                                    value: v,
                                    symbol: 'image://static/image/zhu2.svg',
                                    symbolSize: [20 * this.relativeRate, v / maxVal * maxY * this.relativeRate]
                                }
                            })
                        }]
                };
                this.echarts.init(document.getElementById("riseevent")).setOption(option_bar);
            },
            drawPie(obj){
                var assistData = [];
                var seriesData = obj.map((value, index) => {
                    assistData.push({
                        name: value.name,
                        value: value.proportion,
                    });
                    return {
                        name: value.name,
                        value: value.proportion,
                        itemStyle: {
                            normal: {
                                color: new this.echarts.graphic.LinearGradient(0, 0, 0, 1, [{
                                    offset: 0,
                                    color: this.pieColor[index].start
                                }, {
                                    offset: 1,
                                    color: this.pieColor[index].end
                                }], false),
                            },
                        }
                    }
                });
                var option ={
                            background: "#FFFFFF",
                            tooltip: {
                                show: true,
                                backgroundColor: '#FFFFFF',
                                boxShadow: '0 2px 40px 0 rgba(59,64,83,0.30)',
                                borderRadius: 4 * this.relativeRate,
                                textStyle: {
                                    color: '#3B4053',
                                    fontSize: 20 * this.relativeRate,
                                },
                                formatter: '{b}<br/>{c}%',
                                extraCssText: 'box-shadow: 0 2px 40px 0 rgba(59,64,83,0.30)'
                            },
                            series: [{
                                type: "pie",
                                name: "损失金额占比",
                                legendHoverLink: true,
                                hoverAnimation: true,
                                minAngle: 20,
                                z: 10,
                                center: ['50%', '50%'],
                                radius: [112 * this.relativeRate, 271 * this.relativeRate],
                                itemStyle: {
                                    normal: {
                                        borderWidth: 10.5 * this.relativeRate,
                                        borderColor: '#FFFFFF',
                                    }
                                },
                                label: {
                                    normal: {
                                        show: true,
                                        position: "inside",
                                        formatter: x => {
                                            var resultName = x.name;
                                            var reg = this.regval.exec(resultName);
                                            if (reg) {
                                                resultName = resultName.replace(reg[0], "");
                                                if (resultName.length > 3) {
                                                    resultName = resultName.split("");
                                                    resultName.splice(3, 0, reg[0] + "\n");
                                                    resultName = resultName.join("");
                                                    return resultName
                                                } else {
                                                    resultName = resultName.split("");
                                                    resultName.splice(3, 0, "\n" + reg[0]);
                                                    resultName = resultName.join("");
                                                    return resultName
                                                }

                                            }
                                            if (resultName.length > 4) {
                                                resultName = resultName.split("");
                                                resultName.splice(4, 0, "\n");
                                                resultName = resultName.join("");
                                            }
                                            return resultName;
                                        },
                                        color: '#FFF',
                                        fontSize: 24 * this.relativeRate,
                                        rotate: 90,
                                    }
                                },
                                data: seriesData
                            }, {
                                type: "pie",
                                name: "",
                                z: 1,
                                minAngle: 20,
                                silent: true,
                                radius: [0 * this.relativeRate, 130 * this.relativeRate],
                                itemStyle: {
                                    normal: {
                                        color: 'rgba(255,255,255,0)',
                                        borderColor: 'rgba(59, 64, 83, 0.1)',
                                        borderType: 'dashed'
                                    },
                                },
                                label: {
                                    normal: {
                                        show: true,
                                        position: "inside",
                                        formatter: '{c}%',
                                        color: ' #3B4053',
                                        fontSize: 20 * this.relativeRate,
                                        rotate: 90,
                                    }, emphasis: {
                                        show: false
                                    }
                                },
                                data: assistData
                            }]
                        };
                var chart = this.echarts.init(document.getElementById("risepie"));
                chart.setOption(option)
            },
            showCompany() {
                this.tempCompany = this.currentCompany;
                if (this.companys.length == 0) {
                    axios.get('../static/json/companylist.json').then(res => {
                        this.companys = res.data.companyList;
                    })
                }
                this.popupVisible = true;
            },
            confirmCompany: function () {
                this.popupVisible = false;
                this.currentCompany = this.tempCompany;
                this.drawCollection();
            },
            confirmYear: function () {
                this.popupYear = false;
                this.currentYear = this.tempYear;
                this.currentQuarter = this.tempQuarter;
                this.drawCollection();
            },
            showYear() {
                if (this.yearList.length == 0) {
                    for (var i = this.startYear; i <= this.endYear; i++) {
                        this.yearList.push(i + "年")
                    }
                }
                this.slots[0].values = this.yearList;
                this.popupYear = true;
            },
            btnSelect(company) {
                this.tempCompany = company
            },
            cancel() {
                this.popupVisible = false;
                this.popupYear = false;
            },
            onValuesChange(picker, values) {
                if (values[0]) {
                    this.tempYear = values[0].substring(0, 4);
                }
                var quarter = values[1].substring(1, 2);
                this.tempQuarter = (quarter == '一' ? 1 : quarter == '二' ? 2 : quarter == '三' ? 3 : 4);
            }
        }
    }
</script>
