    }

    .spliter {
        width: 100%;
        height: 0.2rem;
        background: #eee;
    }

    /*滑动表格*/
    .table-title-name {
        height: 1.1rem;
        width: 2.2rem;
        border: 0.01rem solid rgba(59, 64, 83, 0.10);
        text-align: center;
        display: flex;
        justify-content: center;
        align-items: center;
        box-sizing: border-box;
