<!--suppress ALL -->
<template>
    <div>
        <mt-header title="内控管理报告" :fixed="true">
            <router-link to="/" slot="left" >
                <mt-button>
                    <img class="header-img" src="static/image/ic_navbar_back@2x.png">
                </mt-button>
            </router-link>
            <router-link to="/" slot="right">
                <mt-button>
                    <img class="header-img" src="static/image/ic_navbar_home@2x.png">
                </mt-button>
            </router-link>
        </mt-header>
        <div class="ban_top"></div>
        <div class="has-footer">
            <!-- four button-->
            <div class="item">
                <mt-button class="item_btn" v-bind:class="{true:'clickColor',false:''}[backColor=='business']"  @click.native.prevent="active='tab-container-1'">总体分析
                </mt-button>
                <mt-button class="item_btn" v-bind:class="{true:'clickColor',false:''}[backColor=='worth']"  @click.native.prevent="active='tab-container-3'">有效性
                </mt-button>
                <mt-button class="item_btn" v-bind:class="{true:'clickColor',false:''}[backColor=='rectification']"  @click.native.prevent="active='tab-container-5'">整改情况
                </mt-button>
            </div>
            <!-- tab-container -->
            <mt-tab-container class="" v-model="active" swipeable>
                <mt-tab-container-item id="tab-container-1">
                    <!--本期评估派工计划-->

                    <div class="part-title">
                        <span class="part_title">本期评估派工计划</span>
                    </div>
                    <div style="margin-top: 20px">
                        <div class="table-scroll"
                             @touchstart.stop
                             @mousedown.stop
                             @touchmove.stop
                             @mousemove.stop
                             @mouseleave.stop
                             @touchend.stop>

                            <div class="table-content-line2">
                                <span class="table-title-type">派工层级</span>
                                <span class="table-title-type">派工时间</span>
                                <span class="table-title-type">自评估派发率</span>
                                <span class="table-title-type">
                                <span class="titletop">本期派发风险点个数</span>
                                 <span class="titlebottom">
                                     <span class="titleboleft">总公司</span>
                                     <span class="titleboright">分公司</span>
                                 </span>
                                </span>
                                <span class="table-title-type">
                                   <span class="titletop">本期派发控制点个数</span>
                                     <span class="titlebottom">
                                         <span class="titleboleft">总公司</span>
                                         <span class="titleboright">分公司</span>
                                     </span>
                                 </span>
                            </div>
                            <div class="table-content-line2 even-odd" v-for="item in dispatchingPlan">
                                <span class="table-content-value">{{item.companyLevel}}</span>
                                <span class="table-content-value">{{item.date }}</span>
                                <span class="table-content-value">{{item.dispatchingPercent}}</span>
                                <span class="table-content-value1">{{item.headRiskNum}}</span>
                                <span class="table-content-value1">{{item.branchRiskNum}}</span>
                                <span class="table-content-value1">{{item.headControlNum}}</span>
                                <span class="table-content-value1">{{item.branchControlNum}}</span>
                            </div>
                        </div>
                    </div>

                    <div class="part-title">
                        <span class="part_title">本期评估总体结果分析</span>
                    </div>
                    <div style="margin-top: 20px">
                        <div class="table-scroll2"
                             @touchstart.stop
                             @mousedown.stop
                             @touchmove.stop
                             @mousemove.stop
                             @mouseleave.stop
                             @touchend.stop>
                            <div class="table-content-line3">
                                实际损失排名前20名的风险点信息汇总

                            </div>
                            <div class="table-content-line1">
                                <span class="table-title-type1" style="width:2rem">编号</span>
                                <span class="table-title-type1">风险点</span>
                                <span class="table-title-type1">流程归属层级</span>
                                <span class="table-title-type1" style="">损失事件涉及损失金额</span>
                                <span class="table-title-type1">损失事件个数</span>
                                <span class="table-title-type1">损失事件涉及非财务损失等级</span>
                                <span class="table-title-type1" style="">主责部门</span>
                                <span class="table-title-type1">固有风险等级</span>
                                <span class="table-title-type1">剩余风险等级</span>
                                <span class="table-title-type1">综合有效性</span>
                            </div>
                            <div class="table-content-line1 even-odd1" v-for="(item, index) in currentEvaluteResult">
                                <span class="table-content-value2" style="width:2rem">{{index+1}}</span>
                                <span class="table-content-value2">{{item.riskPoint}}</span>
                                <span class="table-content-value2">{{item.companyLevel }}</span>
                                <span class="table-content-value2">{{item.lossAmount}}</span>
                                <span class="table-content-value2">{{item.eventNum}}</span>
                                <span class="table-content-value2">{{item.nonFinancialLevel}}</span>
                                <span class="table-content-value2">{{item.responsibleDepartment}}</span>
                                <span class="table-content-value2">{{item.inherentRiskLevel}}</span>
                                <span class="table-content-value2">{{item.residualRisk}}</span>
                                <span class="table-content-value2">{{item.comprehensiveValid}}</span>
                            </div>
                        </div>
                    </div>
                </mt-tab-container-item>
                <mt-tab-container-item id="tab-container-3">
                    <!--截止到统计时间-->
                    <!--<div class="time_last">统计时间:截止<span>{{loadDate}}</span></div>-->
                    <div class="part-title">
                        <span class="part_title">本期控制综合有效性结果占比情况统计</span>
                    </div>
                    <div id="pic3" style="width:6rem;height:6.36rem;margin-top: .5rem"></div>
                    <div class="fenge_two"></div>
                    <div class="part-title">
                        <span class="part_title">“实质性漏洞”“重大缺陷”有效性结果分布</span>
                    </div>
                    <div style="margin-top: 20px">
                        <div class="table-scroll4"
                             @touchstart.stop
                             @mousedown.stop
                             @touchmove.stop
                             @mousemove.stop
                             @mouseleave.stop
                             @touchend.stop>
                            <div class="table-content-line4">
                                <span class="table-title-type4" style="width:2rem">编号</span>
                                <span class="table-title-type4">控制点</span>
                                <span class="table-title-type4">机构</span>
                                <span class="table-title-type4" style="">控制有效性</span>
                                <span class="table-title-type4">主责部门</span>
                            </div>
                            <div class="table-content-line4 even-odd1" v-for="(item, index) in effictiveResult">
                                <span class="table-content-value4" style="width:2rem">{{index+1}}</span>
                                <span class="table-content-value4">{{item.controlPoint}}</span>
                                <span class="table-content-value4">{{item.organization}}</span>
                                <span class="table-content-value4">{{item.controlEffictive}}</span>
                                <span class="table-content-value4">{{item.responsibleDepartment}}</span>
                            </div>
                        </div>
                    </div>
                    <div class="fenge_two"></div>
                    <div class="part-title">
                        <span class="part_title">控制有效性结果分布情况(按照机构)</span>
                    </div>
                    <div style="margin-top: 20px">
                        <div class="table-scroll3"
                             @touchstart.stop
                             @mousedown.stop
                             @touchmove.stop
                             @mousemove.stop
                             @mouseleave.stop
                             @touchend.stop>
                            <div class="table-content-line3">
                                <span class="table-title-type3" >序号</span>
                                <span class="table-title-type3">分公司</span>
                                <span class="table-title-type3" style="width:8rem;">
                                <span class="titletop1" >控制有效性结果</span>
                                 <span class="titlebottom1">
                                     <span class="titleboleft1">实质性漏洞</span>
                                     <span class="titleboright1">重大缺陷</span>
                                     <span class="titleboleft1">一般缺陷</span>
                                     <span class="titleboright1">合格</span>
                                 </span>
                            </span>
                            </div>
                            <div class="table-content-line3 even-odd" v-for="(item,index) in effictiveResultByInstitution">
                                <span class="table-content-value3">{{index+1}}</span>
                                <span class="table-content-value3">{{item.company}}</span>
                                <span class="table-content-value3">{{item.materialHole }}</span>
                                <span class="table-content-value3">{{item.greatDefect}}</span>
                                <span class="table-content-value3">{{item.commonDefect}}</span>
                                <span class="table-content-value3">{{item.qualify}}</span>
                            </div>
                        </div>
                    </div>
                    <div class="part-title">
                        <span class="part_title">控制有效性结果分布情况(按照内控分类)</span>
                    </div>
                    <div class="table-scroll5"
                         @touchstart.stop
                         @mousedown.stop
                         @touchmove.stop
                         @mousemove.stop
                         @mouseleave.stop
                         @touchend.stop>
                        <div class="table-content-line5">
                            <span class="table-title-type5"style="width:2rem">序号	</span>
                            <span class="table-title-type5">内控分类</span>
                            <span class="table-title-type5" style="width:4rem;">
                                <span class="titletop1" >控制点个数</span>
                                 <span class="titlebottom1">
                                     <span class="titleboleft1">Key</span>
                                     <span class="titleboright1">Normal</span>
                                 </span>
                            </span>
                            <span class="table-title-type5" style="width:8rem;">
                                <span class="titletop1" >控制有效性结果</span>
                                 <span class="titlebottom1">
                                     <span class="titleboleft1">实质性漏洞</span>
                                     <span class="titleboright1">重大缺陷</span>
                                     <span class="titleboleft1">一般缺陷</span>
                                     <span class="titleboright1">合格</span>
                                 </span>
                            </span>
                        </div>
                        <div class="table-content-line5 even-odd" v-for="(item, index) in effictiveResultByControl">
                            <span class="table-content-value5"style="width:2rem">{{index+1}}</span>
                            <span class="table-content-value5">{{item.controlClassfy}}</span>
                            <span class="table-content-value5"style="width: 2rem;">{{item.controlKey}}</span>
                            <span class="table-content-value5"style="width: 2rem;">{{item.controlNormal }}</span>
                            <span class="table-content-value5"style="width: 2rem;">{{item.materialHole}}</span>
                            <span class="table-content-value5"style="width: 2rem;">{{item.greatDefect}}</span>
                            <span class="table-content-value5"style="width: 2rem;">{{item.commonDefect}}</span>
                            <span class="table-content-value5"style="width: 2rem;">{{item.qualify}}</span>
                        </div>
                    </div>
                    <div class="part-title">
                        <span class="part_title">控制有效性结果分布情况(按照一级流程)</span>
                    </div>
                    <div class="table-scroll5"
                         @touchstart.stop
                         @mousedown.stop
                         @touchmove.stop
                         @mousemove.stop
                         @mouseleave.stop
                         @touchend.stop>
                        <div class="table-content-line5">
                            <span class="table-title-type5"style="width:2rem">序号	</span>
                            <span class="table-title-type5">一级流程</span>
                            <span class="table-title-type5">流程归属层级</span>
                            <span class="table-title-type5" style="width:4rem;">
                                <span class="titletop1" >控制点个数</span>
                                 <span class="titlebottom1">
                                     <span class="titleboleft1">Key</span>
                                     <span class="titleboright1">Normal</span>
                                 </span>
                            </span>
                            <span class="table-title-type5" style="width:8rem;">
                                <span class="titletop1" >控制有效性结果</span>
                                 <span class="titlebottom1">
                                     <span class="titleboleft1">实质性漏洞</span>
                                     <span class="titleboright1">重大缺陷</span>
                                     <span class="titleboleft1">一般缺陷</span>
                                     <span class="titleboright1">合格</span>
                                 </span>
                            </span>
                        </div>
                        <div class="table-content-line5 even-odd" v-for="(item, index) in effictiveResultByProcess">
                            <span class="table-content-value5"style="width:2rem">{{index+1}}</span>
                            <span class="table-content-value5">{{item.level1Process}}</span>
                            <span class="table-content-value5">{{item.companyLevel}}</span>
                            <span class="table-content-value5"style="width: 2rem;">{{item.controlKey}}</span>
                            <span class="table-content-value5"style="width: 2rem;">{{item.controlNormal }}</span>
                            <span class="table-content-value5"style="width: 2rem;">{{item.materialHole}}</span>
                            <span class="table-content-value5"style="width: 2rem;">{{item.greatDefect}}</span>
                            <span class="table-content-value5"style="width: 2rem;">{{item.commonDefect}}</span>
                            <span class="table-content-value5"style="width: 2rem;">{{item.qualify}}</span>
                        </div>
                    </div>
                </mt-tab-container-item>
                <mt-tab-container-item id="tab-container-5">
                    <div class="part-title">
                        <span class="part_title">风险应对（整改）</span>
                    </div>
                    <div id="pic5" style="height: 6.5rem;width:6rem"></div>
                    <div class="fenge_two"></div>
                    <div class="part-title">
                        <span class="part_title">本期风险点应对情况统计</span>
                    </div>
                    <div class="table-scroll6"
                         @touchstart.stop
                         @mousedown.stop
                         @touchmove.stop
                         @mousemove.stop
                         @mouseleave.stop
                         @touchend.stop>
                        <div class="table-content-line6">
                            <span class="table-title-type6">截止目前<br>本期自评估完成率	</span>
                            <span class="table-title-type6">风险点测试<br>合格个数</span>
                            <span class="table-title-type6" style="width:6rem;">
                                <span class="titletop1" >本期风险点测试存在缺陷情况下</span>
                                 <span class="titlebottom1">
                                     <span class="titleboleft1">新增整改措施个数</span>
                                     <span class="titleboright1">未增加整改措施个数</span>
                                 </span>
                            </span>
                            <span class="table-title-type6">截止目前<br>本期整改完成率</span>
                        </div>
                        <div class="table-content-line6 even-odd" v-for="item in riskPointResponse">
                            <span class="table-content-value6">{{item.assessmentCompletionRate}}</span>
                            <span class="table-content-value6">{{item.qualifyNum}}</span>
                            <span class="table-content-value6"style="width: 3rem;">{{item.newMeasures }}</span>
                            <span class="table-content-value6"style="width: 3rem;">{{item.noMeasures}}</span>
                            <span class="table-content-value6">{{item.rectificationCompleteRate}}</span>
                        </div>
                    </div>
                </mt-tab-container-item>
            </mt-tab-container>
        </div>
        <div class="footer">
            <div class="footer1">
                <span @click="showCompany" class="company-show">{{currentCompany.codeName}}</span>
                <span @click="showCompany" class="company-show">{{currentCompany.codeName}}</span>

                <span @click="showYear" class="company-show">{{currentYear}}</span>
            </div>
            <div class="footer2">
                <span @click="showQuarter" class="company-show">{{currentQuarter}}</span>
                <span @click="" class="company-show">查询</span>
            </div>
        </div>
        <mt-popup
                v-model="popupVisible"
                class="popup"
                position="bottom">
            <div class="select-title">
                <span class="cancel" @click="cancelCompany">取消</span>
                <span>选择机构</span>
                <span class="confirm" @click="confirmCompany">确认</span>
            </div>
            <div class="select-company">
                <button v-for="company in companys" @click="btnSelect(company)" :class="[company==tempCompany?'selected':'']">{{company}}</button>
                <div class="blank-fill"></div>
            </div>

        </mt-popup>
        <mt-popup
                v-model="popupQuarterVisible"
                class="popup"
                position="bottom">
            <div class="select-title">
                <span class="cancel" @click="cancelQuarter">取消</span>
                <span>选择季度</span>
                <span class="confirm" @click="confirmQuarter">确认</span>
            </div>
            <div class="select-company">
                <button v-for="item in quarters" @click="btnQuarterSelect(item)":class="[item==tempQuarter?'selected':'']">{{item}}</button>
                <div class="blank-fill"></div>
            </div>

        </mt-popup>
        <mt-popup
                v-model="popupYearVisible"
                class="popup"
                position="bottom">
            <div class="select-title">
                <span class="cancel" @click="cancelYear">取消</span>
                <span>选择年份</span>
                <span class="confirm" @click="confirmYear">确认</span>
            </div>
            <div class="select-company">
                <button v-for="item in Years" @click="btnYearSelect(item)":class="[item==tempYear?'selected':'']">{{item}}</button>
                <div class="blank-fill"></div>
            </div>

        </mt-popup>

    </div>
</template>

<script>
    export default {
        data(){
            return {
                dispatchingPlan:[
                    {
                        companyLevel:'总公司',
                        date:'2018/3/1',
                        dispatchingPercent:'1.80%',
                        headRiskNum:'10',
                        branchRiskNum:'2',
                        headControlNum:'11',
                        branchControlNum:'2'
                    }
                ],
                currentEvaluteResult:[
                    {
                        riskPoint:'参选供应商',
                        companyLevel:'总公司',
                        lossAmount:'100',
                        eventNum:'11',
                        nonFinancialLevel:'一般',
                        responsibleDepartment:'风险管理部门',
                        inherentRiskLevel:'高风险',
                        residualRisk:'高风险',
                        comprehensiveValid:'重大缺陷'
                    }
                ],
                inherentRiskS:[
                    {
                        'level1': "2",
                        'level2': "1",
                        'level3': "1",
                        'level4': "1",
                        'level5': "1"
                    },
                    {
                        'level1': "2",
                        'level2': "1",
                        'level3': "1",
                        'level4': "1",
                        'level5': "1"
                    },
                    {
                        'level1': "2",
                        'level2': "1",
                        'level3': "1",
                        'level4': "1",
                        'level5': "1"
                    },
                    {
                        'level1': "2",
                        'level2': "1",
                        'level3': "1",
                        'level4': "1",
                        'level5': "1"
                    },
                    {
                        'level1': "2",
                        'level2': "1",
                        'level3': "1",
                        'level4': "1",
                        'level5': "1"
                    }
                ],
                inherentRiskPercent:[
                    {value:335, name:'极高风险'},
                    {value:310, name:'高风险'},
                    {value:234, name:'中等风险'},
                    {value:135, name:'低风险'},
                ],
                regval:/\d+/,
                pieColor :[
                    {
                        start:'#7B88FF',
                        end:'#204BEF'
                    },{
                        start:'#1880FE',
                        end:'#002778'
                    },{
                        start:'#0098D0',
                        end:'#005FA3'
                    },{
                        start:'#86EACD',
                        end:'#3FC391'
                    },{
                        start:'#3CA9F1',
                        end:'#1188D7'
                    },{
                        start:'#69E4FA',
                        end:'#38C5F3'
                    }
                ],
                effictiveResult:[
                    {controlPoint:'项目存在开发商企业资',
                        organization:'总公司' ,
                        controlEffictive:'重大缺陷',
                        responsibleDepartment:'风险管理部'
                    },
                    {controlPoint:'数据错误或不合规',
                        organization:'总公司' ,
                        controlEffictive:'实质性漏洞',
                        responsibleDepartment:'风险管理部'
                    }
                ],
                currentResidualRiskLevel:[
                    {
                        currentData: '60%',
                        lastData: '50',
                        change: "10%",
                        trend: '上升'
                    },
                    {
                        currentData:'60%',
                        lastData:'50',
                        change:"10%",
                        trend:'上升'
                    },
                    {
                        currentData:'60%',
                        lastData:'50',
                        change:"10%",
                        trend:'上升'
                    },
                    {
                        currentData:'60%',
                        lastData:'50',
                        change:"10%",
                        trend:'上升'
                    }
                ],
                residualRiskDistribute:[
                    {
                        level1Process: '集中采购管理',
                        companyLevel: '总公司',
                        riskNum: '3',
                        highestRisk: '',
                        highRisk: '25%',
                        mediumRisk: '',
                        lowerRisk: '75%'
                    },
                    {   level1Process:'集中采购管理',
                        companyLevel:'总公司',
                        riskNum:'3',
                        highestRisk:'',
                        highRisk:'25%',
                        mediumRisk:'',
                        lowerRisk:'75%'
                    }
                ],
                riskPointResponse:[
                    {
                        assessmentCompletionRate:'20.00%',
                        qualifyNum:'1',
                        newMeasures:"3",
                        noMeasures:'2',
                        rectificationCompleteRate:'66.67%'
                    }
                ],
                effictiveResultByInstitution:[
                    {company:'总公司',
                        materialHole:'2',
                        greatDefect:'3',
                        commonDefect:'3',
                        qualify:'4'

                    }
                ],
                effictiveResultByControl:[
                    {   controlClassfy:'总公司',
                        controlKey:'2',
                        controlNormal:'3',
                        materialHole:'2',
                        greatDefect:'3',
                        commonDefect:'3',
                        qualify:'4'

                    }
                ],
                effictiveResultByProcess:[
                    {  level1Process:'投资管理',
                        companyLevel:'总公司',
                        controlKey:'2',
                        controlNormal:'3',
                        materialHole:'2',
                        greatDefect:'3',
                        commonDefect:'3',
                        qualify:'4'

                    }
                ],
                popupVisible:false,//公司选择弹是否框显示
                popupQuarterVisible:false,//季度选择框是否显示
                popupYearVisible:false,//年份选择框是否显示
                currentCompany:{
                    codeName:"总公司"
                },//当前所选机构
                tempCompany:'',//中间机构变量，切换机构时使用
                currentYear:'2018',
                currentQuarter:'第一季度',
                quarters:['第一季度','第二季度','第三季度','第四季度'],
                tempQuarter:'',
                companys:['总公司','分公司1','分公司2','分公司3'],
                Years:['2013','2014','2015','2016','2017','2018','2019'],
                tempYear:'',
                imgHome:'static/image/ic_navbar_home@2x.png',
                selected:'1',
                fixed:true,
                backColor:'',//按钮背景色
                active : 'tab-container-1',


            }
        },
        watch:{
            active:function (value,oldValue) {
                var that=this;
                if(value=='tab-container-1'){
                    window.setTimeout(function() {
                        that.backColor = 'business';
                        var _data = {
                            "body":{
                                "dimensionid": "1"
                            }
                        };
                    },200);
                }
