<template>
  <div id="app">
    <keep-alive>
      <router-view v-if="$route.meta.keepAlive"></router-view>
    </keep-alive>
    <router-view v-if="!$route.meta.keepAlive"></router-view>
  </div>
</template>

<script>
export default {
  	name: 'app',
  	data(){
  		return {
  			argsObj:{}
  		}
  	},
  	created:function(){
	  	var _query = decodeURI(window.location.search.substr(1));
		  if (_query != '') {
  		  var args = _query.split(/&|=/);
  		  for (var i = 0; i < args.length; i += 2) {
  		      this.argsObj[args[i]] = args[i + 1];
		    }
		  }
      !this.argsObj.name&&(this.argsObj.name="风控移动报表")
	  }
}
</script>

<style>
#app {
  width: 100%;
  height: 100%;
}
</style>



