<script>
    export default {
        data(){
            return {
                dispatchingPlan:[
                    {
                        companyLevel:'总公司',
                        date:'2018/3/1',
                        dispatchingPercent:'1.80%',
                        headRiskNum:'10',
                        branchRiskNum:'2',
                        headControlNum:'11',
                        branchControlNum:'2'
                    }
                ],
                currentEvaluteResult:[
                    {
                        riskPoint:'参选供应商',
                        companyLevel:'总公司',
                        lossAmount:'100',
                        eventNum:'11',
                        nonFinancialLevel:'一般',
                        responsibleDepartment:'风险管理部门',
                        inherentRiskLevel:'高风险',
                        residualRisk:'高风险',
                        comprehensiveValid:'重大缺陷'
                    }
                ],
                inherentRiskS:[
                    {
                        'level1': "2",
                        'level2': "1",
                        'level3': "1",
                        'level4': "1",
                        'level5': "1"
                    },
                    {
                        'level1': "2",
                        'level2': "1",
                        'level3': "1",
                        'level4': "1",
                        'level5': "1"
                    },
                    {
                        'level1': "2",
                        'level2': "1",
                        'level3': "1",
                        'level4': "1",
                        'level5': "1"
                    },
                    {
                        'level1': "2",
                        'level2': "1",
                        'level3': "1",
                        'level4': "1",
                        'level5': "1"
                    },
                    {
                        'level1': "2",
                        'level2': "1",
                        'level3': "1",
                        'level4': "1",
                        'level5': "1"
                    }
                ],
                inherentRiskPercent:[
                    {value:335, name:'极高风险'},
                    {value:310, name:'高风险'},
                    {value:234, name:'中等风险'},
                    {value:135, name:'低风险'},
                ],
                regval:/\d+/,
                pieColor : [
                    {
                        start:'#50A2FF',
                        end:'#116DCF'
                    },{
                        start:'#B093FF',
                        end:'#7F61D9'
                    },{
                        start:'#F09D87',
                        end:'#DD5D47'
                    },{
                        start:'#F6DF9A',
                        end:'#E6B54D'
                    },{
                        start:'#6FF5E3',
                        end:'#32C9B0'
                    },{
                        start:'#6F90A7',
                        end:'#476379'
                    }
                ],
                effictiveResult:[
                    {controlPoint:'项目存在开发商企业资',
                      organization:'总公司' ,
                       controlEffictive:'重大缺陷',
                        responsibleDepartment:'风险管理部'
                    },
                    {controlPoint:'数据错误或不合规',
                        organization:'总公司' ,
                        controlEffictive:'实质性漏洞',
                        responsibleDepartment:'风险管理部'
                    }
                ],
                currentResidualRiskLevel:[
                    {
                        currentData: '60%',
                        lastData: '50',
                        change: "10%",
                        trend: '上升'
                    },
                    {
                        currentData:'60%',
                        lastData:'50',
                        change:"10%",
                        trend:'上升'
                    },
                    {
                        currentData:'60%',
                        lastData:'50',
                        change:"10%",
                        trend:'上升'
                    },
                    {
                        currentData:'60%',
                        lastData:'50',
                        change:"10%",
                        trend:'上升'
                    }
                ],
                residualRiskDistribute:[
                    {
                        level1Process: '集中采购管理',
                        companyLevel: '总公司',
                        riskNum: '3',
                        highestRisk: '',
                        highRisk: '25%',
                        mediumRisk: '',
